//GET USER 
export const getUserDetailRequest = () => ({
    type: GET_USER_DETAIL_REQUEST
})

export const getUserDetailSuccess = data => ({
    type: GET_USER_DETAIL_SUCCESS,
    payload: data
});

export const getUserDetailFailed = error => ({
    type: GET_USER_DETAIL_FAILED,
    payload: error
});

export const getUserDetail = (id,token) => {
    return async dispatch => {
        dispatch(getUserDetailRequest());
        try {
            let config = {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                    Authorization: "Bearer " + token
                },
            };

            let response = await fetch(`${HOST}/users/${id}`, config);
            let json = await response.json();
            console.log('GET USER DETAIL', json)
            if (json.id) {
                dispatch(getUserDetailSuccess(json))
            } else {
                dispatch(getUserDetailFailed(json.message))
            }

        } catch (error) {
            console.log("error", error);
            dispatch(getUserDetailFailed(error))
        }
    };
}

//UPDATE USER
export const updateUserRequest = () => ({
    type: UPDATE_USER_REQUEST
})

export const updateUserSuccess = data => ({
    type: UPDATE_USER_SUCCESS,
    payload: data
});

export const updateUserFailed = error => ({
    type: UPDATE_USER_FAILED,
    payload: error
});

export const updateUser = (id, body, token) => {
    return async dispatch => {
        dispatch(updateUserRequest())
        try {
            let config = {
                method: "PUT",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                    Authorization: "Bearer " + token
                },
                body: body
            };

            let response = await fetch(`${HOST}/users/${id}`, config)
            let json = await response.json();
            console.log('update user', json)
            if (json.status && json.status === true) {
                dispatch(updateUserSuccess(json.user))
            } else {
                dispatch(updateUserFailed(json.message))
            }
        } catch (error) {
            console.log(error)
            dispatch(updateUserFailed(error))
        }
    }
}


