import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";
import TableUser from "../../../components/Table/TableUser";
import Loading from "../../../components/Loading";

import { connect } from "react-redux";
import { getListUserClient } from "../../../store/actions/listUserAction";
import { CLIENT } from "../../../config/constAct";
import MySnackBar from "../../../components/SnackBar";

class ListUserClient extends React.Component {
  componentDidMount() {
    const { accessToken, current_page_client } = this.props
    this.props.onGetListUserClient(accessToken, current_page_client);
  }

  onSelectTable = detail => {
    this.navigate("/main/listuser/detail", detail.id);
  };

  navigate = (name, param) => {
    this.props.history.push({
      pathname: name,
      state: {
        id: param,
        type: CLIENT
      }
    });
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isGet && !!!nextProps.getSuccess && nextProps.errorGet) {
      this.openSnackBar("error", `Lấy danh sách user thất bại ! ${nextProps.errorGet}`);
    }
  }

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  onNext = () => {
    const { accessToken, current_page_client } = this.props
    this.props.onGetListUserClient(accessToken, current_page_client + 1);
  }

  onPrevious = () => {
    const { accessToken, current_page_client } = this.props
    this.props.onGetListUserClient(accessToken, current_page_client - 1);
  }

  render() {
    const { classes, isGet, listClient } = this.props;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Danh sách người dùng</h4>
            </CardHeader>
            <CardBody>
              {isGet ? (
                <Loading />
              ) : (
                  <TableUser
                    isClient
                    onSelectTable={this.onSelectTable}
                    tableHeaderColor="primary"
                    tableHead={["Tài khoản", "Tên"]}
                    tableData={listClient}
                    onPrevious={this.onPrevious}
                    onNext={this.onNext}
                  />
                )}
            </CardBody>
            <CardFooter
              style={{ justifyContent: "flex-end", alignItems: "flex-end" }}
            >
              <Button color="primary" onClick={() => this.navigate("/main/listuser/create")}>
                Tạo mới
              </Button>
            </CardFooter>
          </Card>
        </GridItem>
        <MySnackBar ref={"MySnackBar"} />
      </GridContainer>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};

const mapStateToProps = state => {
  return {
    listClient: state.userDetailReducer.listClient,
    isGet: state.userDetailReducer.isGet,
    getSuccess: state.userDetailReducer.getSuccess,
    errorGet: state.userDetailReducer.errorGet,
    accessToken: state.profileReducer.accessToken,
    current_page_client: state.userDetailReducer.current_page_client
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetListUserClient: (accessToken, page) => {
      dispatch(getListUserClient(accessToken, page));
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(ListUserClient);

export default withStyles(styles)(redux);
