import { REQUEST_MANAGER, REQUEST_MANAGER_SUCCESS, REQUEST_MANAGER_FAILED, HOST, REMOVE_REQUEST, GET_VERIFY_MANAGER_REQUEST, GET_VERIFY_MANAGER_SUCCESS, GET_VERIFY_MANAGER_FAILED, GET_DETAIL_VERIFY_REQUEST, GET_DETAIL_VERIFY_SUCCESS, GET_DETAIL_VERIFY_FAILED, CHANGE_VERIFY_MANAGER_REQUEST, CHANGE_VERIFY_MANAGER_SUCCESS, CHANGE_VERIFY_MANAGER_FAILED, REMOVE_DATA_VERIFY } from "../../config/constAct";

//REQUEST MANAGER
export const requestManagerRequest = () => ({
    type: REQUEST_MANAGER
})

export const requestManagerSuccess = (data) => ({
    type: REQUEST_MANAGER_SUCCESS,
    payload: data
})

export const requestManagerFailed = error => ({
    type: REQUEST_MANAGER_FAILED,
    payload: error
})

export const remove = () => ({
    type: REMOVE_REQUEST
})

export const removeRequest = () => {
    return async dispatch => {
        dispatch(remove())
    }
}

export const requestManager = (body) => {
    return async dispatch => {
        dispatch(requestManagerRequest())
        try {
            let config = {
                method: "POST",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                body: body
            };
            let response = await fetch(`${HOST}/users/verifying-managers`, config);
            let json = await response.json();
            console.log("login", json);
            if (json.status && json.status === true) {
                dispatch(requestManagerSuccess(json.manager));
            } else {
                dispatch(requestManagerFailed(json.message));
            }
        } catch (error) {
            dispatch(requestManagerFailed(error))
        }
    }
}

//GET LIST VERIFY
export const getVerifyManagerRequest = () => ({
    type: GET_VERIFY_MANAGER_REQUEST
})

export const getVerifyManagerSuccess = data => ({
    type: GET_VERIFY_MANAGER_SUCCESS,
    payload: data
})

export const getVerifyManagerFailed = error => ({
    type: GET_VERIFY_MANAGER_FAILED,
    payload: error
})

export const getVerifyManager = (page, token) => {
    return async dispatch => {
        console.log('token', token)
        dispatch(getVerifyManagerRequest())
        try {
            let config = {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                    Authorization: "Bearer " + token
                },
            };
            let response = await fetch(`${HOST}/users/verifying-managers?page_size=10&page=${page}`, config);
            let json = await response.json();
            console.log("getverify", json);
            if (json.content) {
                dispatch(getVerifyManagerSuccess(json));
            } else {
                dispatch(getVerifyManagerFailed(json.message));
            }
        } catch (error) {
            dispatch(getVerifyManagerFailed(error))
        }
    }
}

