//CHANGE STATUS
export const changeVerifyManagerRequest = () => ({
    type: CHANGE_VERIFY_MANAGER_REQUEST
})

export const changeVerifyManagerSuccess = data => ({
    type: CHANGE_VERIFY_MANAGER_SUCCESS,
    payload: data
})

export const changeVerifyManagerFailed = error => ({
    type: CHANGE_VERIFY_MANAGER_FAILED,
    payload: error
})

export const changeVerifyManager = (id, body, token) => {
    return async dispatch => {
        dispatch(changeVerifyManagerRequest())
        try {
            let config = {
                method: "POST",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                    Authorization: "Bearer " + token
                },
                body: body
            };
            let response = await fetch(`${HOST}/users/verifying-managers/${id}`, config);
            let json = await response.json();
            console.log("login", json);
            if (json.status) {
                dispatch(changeVerifyManagerSuccess(json.user));
            } else {
                dispatch(changeVerifyManagerFailed(json.message));
            }
        } catch (error) {
            dispatch(changeVerifyManagerFailed(error))
        }
    }
}

