import React, { Component } from "react";
import { connect } from "react-redux";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import TableReview from "../../../../components/Table/TableReview";
import {Grid} from "@material-ui/core"

import { getReview } from "../../../../store/actions/detailHotelAction";
import Loading from "../../../../components/Loading";

class ReviewContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 0
    };
  }

  componentDidMount() {
    const { id } = this.props;
    this.props.onGetReview(id, 10);
  }

  render() {
    const { classes, review, isLoadingReview } = this.props;
    return (
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>Đánh giá </h4>
        </CardHeader>
        <CardBody>
          {isLoadingReview && <Loading />}
          {review && review.length !== 0 && (
            <TableReview
              onSelectTable={this.onSelectTable}
              tableHeaderColor="primary"
              tableHead={["Tên", "Điểm", "Thời gian", "Nội dung"]}
              tableData={review}
            />
          )}
          {review && review.length === 0 &&
            <Grid style={{ display: 'flex', justifyContent: 'center' }}>
              <h1 style={{ fontSize: 18 }}>Chưa có đánh giá nào </h1>
            </Grid>}
        </CardBody>
      </Card>
    );
  }
}

const mapStateToProps = state => {
  return {
    review: state.detailHotelReducer.review,
    isLoadingReview: state.detailHotelReducer.isLoadingReview
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetReview: (id, number) => {
      dispatch(getReview(id, number));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReviewContainer);
