import {
  HOST, GET_LIST_USER_MANAGER_REQUEST, GET_LIST_USER_MANAGER_SUCCESS, GET_LIST_USER_MANAGER_FAILED, GET_LIST_USER_CLIENT_REQUEST, GET_LIST_USER_CLIENT_SUCCESS, GET_LIST_USER_CLIENT_FAILED,
} from "../../config/constAct";

//GET LIST USER MANAGER
export const getListUserManagerRequest = () => ({
  type: GET_LIST_USER_MANAGER_REQUEST
});

export const getListUserManagerSuccess = data => ({
  type: GET_LIST_USER_MANAGER_SUCCESS,
  payload: data
});

export const getListUserManagerFailed = error => ({
  type: GET_LIST_USER_MANAGER_FAILED,
  payload: error
});

export const getListUserManager = (token, page) => {
  return async dispatch => {
    dispatch(getListUserManagerRequest());
    try {
      let config = {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token
        }
      };
      let response = await fetch(`${HOST}/users/managers?page_size=10&page=${page}`, config);
      let json = await response.json();
      console.log("get", json);
      if (json.content) {
        dispatch(getListUserManagerSuccess(json));
      } else {
        dispatch(getListUserManagerFailed(json.message));
      }
    } catch (error) {
      console.log("error", error);
      dispatch(getListUserManagerFailed(error));
    }
  };
};

