import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Icon from "@material-ui/core/Icon";
// core components

import sidebarStyle from "assets/jss/material-dashboard-react/components/sidebarStyle.js";

class Sidebar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  activeRoute = routeName => {
    const { props } = this;
    return props.location.pathname.indexOf(routeName) > -1 ? true : false;
  };

  renderLink = () => {
    const { props } = this;
    const { classes, color, logo, image, logoText, routes } = props;
    const { show } = this.state;
    var links = (
      <List className={classes.list}>
        {routes.map((prop, key) => {
          if (
            prop.layout === "/main" &&
            prop.path !== "/listuser" &&
            prop.path !== "/listuser/manager" &&
            prop.path !== "/listuser/client"
          ) {
            var activePro = " ";
            var listItemClasses;

            listItemClasses = classNames({
              [" " + classes[color]]: this.activeRoute(prop.layout + prop.path)
            });
            const whiteFontClasses = classNames({
              [" " + classes.whiteFont]: this.activeRoute(
                prop.layout + prop.path
              )
            });
            return (
              <NavLink
                to={
                  prop.path !== "/logout" ? prop.layout + prop.path : "/login"
                }
                className={activePro + classes.item}
                activeClassName="active"
                key={key}
              >
                <ListItem button className={classes.itemLink + listItemClasses}>
                  {typeof prop.icon === "string" ? (
                    <Icon
                      className={classNames(
                        classes.itemIcon,
                        whiteFontClasses,
                        {
                          [classes.itemIconRTL]: props.rtlActive
                        }
                      )}
                    >
                      {prop.icon}
                    </Icon>
                  ) : (
                    <prop.icon
                      className={classNames(
                        classes.itemIcon,
                        whiteFontClasses,
                        {
                          [classes.itemIconRTL]: props.rtlActive
                        }
                      )}
                    />
                  )}
                  <ListItemText
                    primary={props.rtlActive ? prop.rtlName : prop.name}
                    className={classNames(classes.itemText, whiteFontClasses, {
                      [classes.itemTextRTL]: props.rtlActive
                    })}
                    disableTypography={true}
                  />
                </ListItem>
              </NavLink>
            );
          }
          if (prop.path == "/listuser/manager" || prop.path == "/listuser/client") {
            var activePro = " ";
            var listItemClasses;

            listItemClasses = classNames({
              [" " + classes[color]]: this.activeRoute(prop.layout + prop.path)
            });
            const whiteFontClasses = classNames({
              [" " + classes.whiteFont]: this.activeRoute(
                prop.layout + prop.path
              )
            });
            if (show) {
              return (
                <NavLink
                  to={
                    prop.layout + prop.path
                  }
                  className={activePro + classes.item}
                  activeClassName="active"
                  key={key}
                >
                  <ListItem
                    // button
                    className={classes.itemLink + listItemClasses}
                  >
                    {typeof prop.icon === "string" ? (
                      <Icon
                        className={classNames(
                          classes.itemIcon,
                          whiteFontClasses,
                          {
                            [classes.itemIconRTL]: props.rtlActive
                          }
                        )}
                      >
                        {prop.icon}
                      </Icon>
                    ) : (
                      <prop.icon
                        className={classNames(
                          classes.itemIcon,
                          whiteFontClasses,
                          {
                            [classes.itemIconRTL]: props.rtlActive
                          }
                        )}
                      />
                    )}
                    <ListItemText
                      primary={props.rtlActive ? prop.rtlName : prop.name}
                      className={classNames(
                        classes.itemText,
                        whiteFontClasses,
                        {
                          [classes.itemTextRTL]: props.rtlActive
                        }
                      )}
                      disableTypography={true}
                    />
                  </ListItem>
                </NavLink>
              );
            } else {
              return null;
            }
          } else {
            var activePro = " ";
            var listItemClasses;

            listItemClasses = classNames({
              [" " + classes[color]]: this.activeRoute(prop.layout + prop.path)
            });
            const whiteFontClasses = classNames({
              [" " + classes.whiteFont]: this.activeRoute(
                prop.layout + prop.path
              )
            });
            return (
              <ListItem
                button
                className={classes.itemLink}
                onClick={() => this.setState({ show: !this.state.show })}
              >
                {typeof prop.icon === "string" ? (
                  <Icon
                    className={classNames(classes.itemIcon, whiteFontClasses, {
                      [classes.itemIconRTL]: props.rtlActive
                    })}
                  >
                    {prop.icon}
                  </Icon>
                ) : (
                  <prop.icon
                    className={classNames(classes.itemIcon, whiteFontClasses, {
                      [classes.itemIconRTL]: props.rtlActive
                    })}
                  />
                )}
                <ListItemText
                  primary={props.rtlActive ? prop.rtlName : prop.name}
                  className={classNames(classes.itemText, whiteFontClasses, {
                    [classes.itemTextRTL]: props.rtlActive
                  })}
                  disableTypography={true}
                />
              </ListItem>
            );
          }
        })}
      </List>
    );
    return links;
  }

  render() {
    const { props } = this;
    const { classes, color, logo, image, logoText, routes } = props;
    const { show } = this.state;
    return (
      <div>
        <Hidden mdUp implementation="css">
          <Drawer
            variant="temporary"
            anchor={props.rtlActive ? "left" : "right"}
            open={props.open}
            classes={{
              paper: classNames(classes.drawerPaper, {
                [classes.drawerPaperRTL]: props.rtlActive
              })
            }}
            onClose={props.handleDrawerToggle}
            ModalProps={{
              keepMounted: true // Better open performance on mobile.
            }}
          >
            {/* {brand} */}
            <div className={classes.sidebarWrapper}>
              {/* {props.rtlActive ? <RTLNavbarLinks /> : <AdminNavbarLinks />} */}
              {this.renderLink()}
            </div>
            {image !== undefined ? (
              <div
                className={classes.background}
                style={{ backgroundImage: "url(" + image + ")" }}
              />
            ) : null}
          </Drawer>
        </Hidden>
        <Hidden smDown implementation="css">
          <Drawer
            anchor={props.rtlActive ? "right" : "left"}
            variant="permanent"
            open
            classes={{
              paper: classNames(classes.drawerPaper, {
                [classes.drawerPaperRTL]: props.rtlActive
              })
            }}
          >
            {/* {brand} */}
            <div className={classes.sidebarWrapper}>{this.renderLink()}</div>
            {image !== undefined ? (
              <div
                className={classes.background}
                style={{ backgroundImage: "url(" + image + ")" }}
              />
            ) : null}
          </Drawer>
        </Hidden>
      </div>
    );
  }
}

Sidebar.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(sidebarStyle)(Sidebar);
