import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import { connect } from 'react-redux'
import DetailHotel from "./DetailHotel";
import CreateHotel from "./CreateHotel";
import ListHotel from "./ListHotel";
import { MANAGER } from "../../../config/constAct";
import ManagerHotels from "../User/ManagerHotels";

class HotelController extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { role } = this.props
    return (
      <Switch>
        <Route exact path={`/main/listhotel`} component={role === MANAGER ? ManagerHotels : ListHotel} />
        <Route path={`/main/listhotel/detail`} component={DetailHotel} />
        <Route path={`/main/listhotel/create`} component={CreateHotel} />
      </Switch>
    );
  }
}

const mapStateToProps = state => {
  return {
    role: state.profileReducer.role
  }
}

export default connect(mapStateToProps)(HotelController)