import React, { Component } from "react";
import {
  Button,
  Grid,
  TextField,
  Card,
  CardContent,
  CircularProgress
} from "@material-ui/core";
import withStyles from "@material-ui/core/styles/withStyles";
import loginStyle from "../../assets/jss/material-dashboard-react/containers/loginStyle.js";
import CardMedia from "@material-ui/core/CardMedia";
import GridItem from "../../components/Grid/GridItem";
import { connect } from "react-redux";
import { login } from "../../store/actions/userAction";
import MyDialog from "../../components/Dialog";
import SignUpDialog from "./SignUpDialog.js";

const background = require("../../assets/img/sidebar-2.jpg");
// const background = require("../../../public/img/sidebar-2.jpg");

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
    };
    this.refDialog = React.createRef()
  }
  onLoginWithAccount = () => {
    const user = {
      username: this.state.username,
      password: this.state.password
    };
    this.props.onLogin(JSON.stringify(user));
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isLoading && !!nextProps.loginSuccess && !!!nextProps.errorLogin) {
      this.refs.MyDialog.handleClickOpen("Login Success", "Login successfully");
      this.props.history.push('/main/dashboard')
    }
    if (nextProps && !!!nextProps.isLoading && !!!nextProps.loginSuccess && nextProps.errorLogin) {
      this.refs.MyDialog.handleClickOpen("Login Failed", nextProps.errorLogin);
    }
  }

  openDialog = () => {
    // console.log('object', this.signupDialog)
    // console.log('object2',this.signupDialog)
    // this.refDialog && this.refDialog.current && this.refDialog.current.handleOpen()
    this.signupDialog.handleOpen()
  }

  render() {
    const { username, password } = this.state;
    const { history, classes, isLoading } = this.props;
    return (
      <div className={classes.wrapper}>
        <CardMedia
          image={background}
          style={{ width: "100%", height: "100%" }}
        />
        <Grid
          container
          style={{
            position: "absolute",
            backgroundColor: "rgb(0,0,0,0.6)",
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <GridItem>
            <Grid
              container
              style={{
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <h1 style={{ color: "white" }}>SmartHotel</h1>
            </Grid>
            <Card>
              <CardContent>
                <TextField
                  disabled={isLoading}
                  fullWidth
                  id="standard-address"
                  label="Username"
                  value={username}
                  onChange={event =>
                    this.setState({ username: event.target.value })
                  }
                  margin="normal"
                  variant="outlined"
                />
                <TextField
                  disabled={isLoading}
                  fullWidth
                  value={password}
                  id="standard-address"
                  label="Password"
                  onChange={event =>
                    this.setState({ password: event.target.value })
                  }
                  type="password"
                  margin="normal"
                  variant="outlined"
                />
                <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                  <h1 style={{ fontSize: 16 }}>Chưa có tài khoản    </h1>
                  <h1 onClick={this.openDialog} style={{ fontSize: 16, color: 'purple' }}>{` ?  Đăng ký ngay `}</h1>
                </Grid>
                {isLoading ? (
                  <Grid
                    container
                    style={{
                      justifyContent: "center",
                      alignItems: "center",
                      width: "100%",
                      height: 60
                    }}
                  >
                    <CircularProgress size={30} />
                  </Grid>
                ) : (
                    <Grid style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                      <Button
                        fullWidth
                        style={{
                          backgroundColor: "#9D3AB1",
                          color: "white",
                          width: '50%',
                          height: 50,
                          marginTop: 20
                        }}
                        onClick={this.onLoginWithAccount}
                      >
                        Đăng nhập
                    </Button>
                    </Grid>
                  )}
              </CardContent>
            </Card>
          </GridItem>
        </Grid>
        <MyDialog ref={"MyDialog"} />
        <SignUpDialog innerRef={ref => this.signupDialog = ref} />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isLoading: state.profileReducer.isLoading,
    errorLogin: state.profileReducer.errorLogin,
    loginSuccess: state.profileReducer.loginSuccess
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onLogin: (data) => {
      dispatch(login(data));
    },
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);

export default withStyles(loginStyle)(redux);
