import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";
import {
  Grid,
  IconButton,
  Paper,
  InputBase
} from "@material-ui/core";
import CustomTable from "../../../components/CustomTable";
import FilterListIcon from "@material-ui/icons/FilterList";
import SearchIcon from "@material-ui/icons/Search";
import Close from "@material-ui/icons/Close"
import { connect } from "react-redux";
import {
  getListHotel,
  deleteHotel,
  searchHotelByName
} from "../../../store/actions/listHotelAction";
import MySnackBar from "../../../components/SnackBar";
import Loading from "../../../components/Loading";
import TableHotel from "../../../components/Table/TableHotel";
import MyDialog from '../../../components/Dialog'

class ListHotel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isFilter: false,
      selected: [],
      text: ""
    };
  }

  componentDidMount() {
    this.props.onGetListHotel(this.props.current_page ? this.prop.current_page : 0);
  }

  onSelectTable = detail => {
    this.navigate("/main/listhotel/detail", detail.id);
  };

  onCreate = () => {
    this.navigate("/main/listhotel/create");
  };

  onDelete = () => {
    this.refs.MyDialog.handleClickOpen("Xoá", "Bạn có chắc muốn xoá khách sạn này không ?", false, true)
  };

  delete = () => {
    const { accessToken } = this.props
    const { selected } = this.state;
    this.props.onDeleteHotel(accessToken, selected[0]);
  }

  navigate = (name, param) => {
    this.props.history.push({
      pathname: name,
      state: {
        id: param
      }
    });
  };

  getSelectedList = list => {
    this.setState({ selected: list });
  };

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  onNext = () => {
    this.props.onGetListHotel(this.props.current_page + 1);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps && !!!nextProps.isLoading && !!!nextProps.getSuccess && nextProps.errorGet) {
      this.openSnackBar("error", `Lấy danh sách thất bại ! ${nextProps.errorGet}`);
    }

    if (nextProps && !!!nextProps.isLoading && !!nextProps.deleteSuccess && !!!nextProps.deleteError) {
      this.openSnackBar("success", "Xoá khách sạn thành công");
    }

    if (nextProps && !!!nextProps.isLoading && !!!nextProps.deleteSuccess && nextProps.deleteError) {
      this.openSnackBar("error", `Xoá khách sạn thất bại ! ${nextProps.deleteError}`);
    }
  }

  onPrevious = () => {
    this.props.onGetListHotel(this.props.current_page - 1);
  }

  search = (event) => {
    this.setState({ text: event.target.value })
    this.props.onSearch(event.target.value)
  }

  clear = () => {
    this.setState({ text: "" })
    this.props.onGetListHotel(0);
  }

  render() {
    const { classes, isLoading, current_page } = this.props;
    const { isFilter, selected, text } = this.state;
    return (
      <GridContainer>
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>
                Danh sách các khách sạn
              </h4>
            </CardHeader>
            <CardBody>
              <Grid
                style={{
                  display: "flex",
                  justifyContent: "space-between"
                }}
              >
                <Paper className={classes.root}>
                  <InputBase
                    className={classes.input}
                    placeholder="Tìm kiếm"
                    value={text}
                    onChange={(event => this.search(event))}
                  />
                  {text && text !== '' && <IconButton
                    className={classes.iconButton}
                    onClick={this.clear}
                    aria-label="Search"
                  >
                    <Close />
                  </IconButton>}
                  <IconButton
                    className={classes.iconButton}
                    aria-label="Search"
                  >
                    <SearchIcon />
                  </IconButton>
                </Paper>
                <IconButton
                  aria-label="Filter list"
                  onClick={() =>
                    this.setState({ isFilter: !this.state.isFilter })
                  }
                >
                  <FilterListIcon />
                  <h5 className={classes.cardTitle}>
                    {isFilter ? "Huỷ sắp xếp" : "Sắp xếp"}
                  </h5>
                </IconButton>
              </Grid>

              {isLoading ? (
                <Loading />
              ) : isFilter ? (
                <CustomTable
                  callBackList={this.getSelectedList}
                  current_page={current_page}
                  onPrevious={this.onPrevious}
                  onNext={this.onNext}
                  rows={[
                    {
                      id: "name",
                      label: "Tên"
                    },
                    {
                      id: "address",
                      label: "Địa chỉ"
                    },
                    {
                      id: "ratings",
                      label: "Lượt đánh giá"
                    },
                    {
                      id: "point",
                      label: "Điểm"
                    },
                    {
                      id: "description",
                      label: "Mô tả"
                    }
                  ]}
                />
              ) : (
                    <TableHotel
                      onSelectTable={this.onSelectTable}
                      tableHeaderColor="primary"
                      tableHead={[
                        "Tên ",
                        "Địa chỉ",
                        "Lượt đánh giá",
                        "Điểm",
                        "Mô tả"
                      ]}
                      onPrevious={this.onPrevious}
                      onNext={this.onNext}
                    />
                  )}
            </CardBody>
            <CardFooter
              style={{ justifyContent: "flex-end", alignItems: "flex-end" }}
            >
              {isFilter && selected.length > 0 && (
                <Button
                  style={{ marginRight: 20 }}
                  color="primary"
                  onClick={this.onDelete}
                >
                  Xoá
                </Button>
              )}
              <Button color="primary" onClick={this.onCreate}>
                Tạo mới
              </Button>
            </CardFooter>
          </Card>
        </GridItem>
        <MySnackBar ref={"MySnackBar"} />
        <MyDialog ref={"MyDialog"} delete={this.delete} />
      </GridContainer>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  },
  cardTitle: {
    color: "#00000",
    marginTop: "0px",
    marginLeft: "10px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    fontSize: 18
  },
  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: '100%'
  },
  input: {
    marginLeft: 8,
    flex: 1
  },
  iconButton: {
    padding: 10
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4
  }
};

const mapStateToProps = state => {
  return {
    accessToken: state.profileReducer.accessToken,
    listHotel: state.listHotelReducer.listHotel,
    current_page: state.listHotelReducer.current_page,
    isLoading: state.listHotelReducer.isLoading,
    getSuccess: state.listHotelReducer.getSuccess,
    errorGet: state.listHotelReducer.errorGet,
    total_pages: state.listHotelReducer.total_pages,
    deleteSuccess: state.listHotelReducer.deleteSuccess,
    deleteError: state.listHotelReducer.deleteError,
    
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetListHotel: page => {
      dispatch(getListHotel(page));
    },
    onDeleteHotel: (token, id) => {
      dispatch(deleteHotel(token, id));
    },
    onSearch: name => {
      dispatch(searchHotelByName(name))
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(ListHotel);

export default withStyles(styles)(redux);
