import React, { Component } from 'react'
import {
    TextField,
    FormHelperText
} from '@material-ui/core'

export default class InputField extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: props.value,
            error: ""
        }
    }

    onChangeText = (event) => {
        let text = event.target.value
        const { regex, handleChange } = this.props;
        if (regex) {
            if (!regex.test(text)) {
                this.setState({ error: "Sai định dạng" })
            } else {
                this.setState({ error: "" })
            }
        }
        handleChange(text)
    }

    render() {
        const { label, type } = this.props
        const { error, value } = this.state
        return (
            <div>
                <TextField
                    type={type}
                    id="standard-name"
                    label={label}
                    value={value}
                    onChange={event =>
                        this.onChangeText(event)
                    }
                    margin="normal"
                    fullWidth
                />
                <FormHelperText style={{ color: 'red', paddingTop: 0, marginTop: 0 }}>{error}</FormHelperText>
            </div>
        )
    }
}
