import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from "react-redux";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { ANOTHER_HOST } from '../../../config/constAct';
import avatar from "assets/img/faces/marc.jpg";
import {
  getUserDetail,
  deleteUser,
  removeNow,
} from "../../../store/actions/detailUserAction";

import { TextField, Grid, MenuItem } from "@material-ui/core";
import Loading from "../../../components/Loading";
import MySnackBar from "../../../components/SnackBar";
import MyDialog from "../../../components/Dialog";
import { uploadImage, updateUser } from "../../../store/actions/userAction";

const roles = [
  {
    label: "MANAGER"
  },
  {
    label: "CLIENT"
  }
];

class UserProfileManager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.profile.id,
      isUpdate: false,
      username: "",
      name: "",
      full_name: "",
      role: "",
      email: "",
      phone: ""
    };
  }
  componentDidMount() {
    // this.props.onGetUserDetail(this.state.id,this.props.accessToken);
  }

  componentWillUnmount() {
    this.props.onRemove();
  }

  updateProfile = () => {
    const { profile, accessToken } = this.props;
    this.setState({ isUpdate: !this.state.isUpdate });
    this.props.onUpdateUser(profile.id, this.bodyUpdate(), accessToken);
  };

  bodyUpdate = () => {
    const { full_name, email, phone, name } = this.state;
    const body = {
      email: email,
      full_name: full_name,
      name: name,
      phone: phone
    };

    return JSON.stringify(body);
  };

  changeForm = () => {
    const { profile } = this.props;
    const { isUpdate } = this.state;
    this.setState({
      username: profile.username,
      name: profile.name,
      full_name: profile.full_name,
      email: profile.email,
      phone: profile.phone,
      isUpdate: !isUpdate
    });
  };

  componentWillReceiveProps(nextProps) {
    if (
      nextProps &&
      !!!nextProps.isUpdate2 &&
      !!nextProps.updateSuccess &&
      !!!nextProps.errorUpdate
    ) {
      this.openSnackBar("success", "Cập nhật thành công");
      this.props.onRemove()
      // this.onBack();
    }
    if (
      nextProps &&
      !!!nextProps.isUpdate2 &&
      !!!nextProps.updateSuccess &&
      nextProps.errorUpdate
    ) {
      this.openSnackBar(
        "error",
        `Cập nhật thất bại ! ${nextProps.errorUpdate}`
      );
      this.props.onRemove()
    }

    if (
      nextProps &&
      !!!nextProps.isDeleting &&
      !!nextProps.deleteSuccess &&
      !!!nextProps.errorDelete
    ) {
      this.openSnackBar("success", "Xoá tài khoản thành công");
      setTimeout(() => {
        this.props.history.goBack();
      }, 500);

    }
    if (
      nextProps &&
      !!!nextProps.isDeleting &&
      !!!nextProps.deleteSuccess &&
      nextProps.errorDelete
    ) {
      this.openSnackBar(
        "error",
        `Xoá tài khoản thất bại ! ${nextProps.errorDelete}`
      );
      this.props.onRemove()
    }

    if (nextProps && !!!nextProps.isUpload && !!nextProps.uploadSuccess && !!!nextProps.uploadError) {
      this.openSnackBar("success", "Cập nhật ảnh thành công");
      this.setState({ showUpdateImage: !this.state.showUpdateImage })
      this.props.onRemove()
    }
    if (nextProps && !!!nextProps.isUpload && !!!nextProps.uploadSuccess && nextProps.uploadError) {
      this.openSnackBar("error", `Cập nhật ảnh thất bại ! ${nextProps.uploadError}`);
      this.props.onRemove()
    }
  }

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  delete = () => {
    const { profile, accessToken } = this.props;
    this.refs.MyDialog.handleClickOpen(
      "Xoá tài khoản",
      "Bạn có chắc muốn xoá tài khoản này ",
      false,
      true
    );
  };

  handleChange = event => {
    this.setState({ role: event.target.value });
  };

  updateImage = () => {
    const { file } = this.state;
    const { profile, accessToken } = this.props;
    if (!file) {
      alert("Vui lòng chọn ảnh !");
      return;
    }
    if (file.size > 3000000) {
      alert("Vui lòng chọn ảnh có dung lượng dưới 1MB");
      return;
    }
    var formData = new FormData();
    formData.append("file", file);

    this.props.onUploadImage(profile.id, formData, accessToken);
  };

  render() {
    const {
      classes,
      profile,
      accessToken,
      onDeleteUser,
      isUpdate2,
      isDeleting,
      isUpload
    } = this.props;
    const {
      isUpdate,
      username,
      full_name,
      email,
      phone,
      name,
      showUpdateImage
    } = this.state;
    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={isUpdate ? 12 : 8}>
            <Card>
              <CardHeader color="primary">
                <h4 className={classes.cardTitleWhite}>
                  {isUpdate ? `Chỉnh sửa thông tin` : "Thông tin"}
                </h4>
                <p className={classes.cardCategoryWhite}>
                  Complete your profile
                </p>
              </CardHeader>
              <CardBody>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={8}>
                    <TextField
                      disabled={true}
                      id="standard-name"
                      label="Tài khoản "
                      value={isUpdate ? username : profile.username}
                      onChange={event =>
                        this.setState({ username: event.target.value })
                      }
                      margin="normal"
                      fullWidth
                    />
                  </GridItem>

                  <GridItem xs={12} sm={12} md={4}>
                    <TextField
                      id="standard-select-currency"
                      select
                      label="Chọn quyền"
                      disabled={true}
                      value={profile.role}
                      onChange={this.handleChange}
                      SelectProps={{
                        MenuProps: {
                          className: classes.menu
                        }
                      }}
                      margin="normal"
                      fullWidth
                    >
                      {roles.map(option => (
                        <MenuItem key={option.label} value={option.label}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </TextField>
                  </GridItem>
                </GridContainer>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={6}>
                    <TextField
                      disabled={!isUpdate}
                      id="standard-name"
                      label="Tên"
                      value={isUpdate ? name : profile.name}
                      onChange={event =>
                        this.setState({ name: event.target.value })
                      }
                      margin="normal"
                      fullWidth
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={6}>
                    <TextField
                      disabled={!isUpdate}
                      id="standard-name"
                      label="Tên đầy đủ"
                      value={isUpdate ? full_name : profile.full_name}
                      onChange={event =>
                        this.setState({ full_name: event.target.value })
                      }
                      margin="normal"
                      fullWidth
                    />
                  </GridItem>
                </GridContainer>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={6}>
                    <TextField
                      disabled={!isUpdate}
                      id="standard-name"
                      label="Email"
                      value={isUpdate ? email : profile.email}
                      onChange={event =>
                        this.setState({ email: event.target.value })
                      }
                      margin="normal"
                      fullWidth
                    />
                  </GridItem>
                  <GridItem xs={12} sm={12} md={6}>
                    <TextField
                      disabled={!isUpdate}
                      id="standard-name"
                      label="Số điện thoại"
                      value={isUpdate ? phone : profile.phone}
                      onChange={event =>
                        this.setState({ phone: event.target.value })
                      }
                      margin="normal"
                      fullWidth
                    />
                  </GridItem>
                </GridContainer>
                {showUpdateImage && (
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={4}>
                      <h3
                        style={{
                          fontSize: 16,
                          marginTop: 15,
                          marginBottom: 15,
                          padding: 0
                        }}
                      >
                        {`Cập nhật ảnh đại diện `}
                      </h3>
                    </GridItem>
                    <GridItem xs={12} sm={12} md={4}>
                      <input
                        style={{
                          fontSize: 16,
                          marginTop: 15,
                          marginBottom: 15,
                          padding: 0
                        }}
                        type="file"
                        onChange={event =>
                          this.setState({ file: event.target.files[0] })
                        }
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={4}>
                      <Button
                        disabled={isUpdate2 || isUpload || isDeleting}
                        color="primary"
                        onClick={() => this.updateImage()}
                      >
                        Đăng ảnh
                        </Button>
                    </GridItem>
                  </GridContainer>
                )}
              </CardBody>
              <CardFooter>
                {isUpdate ? (
                  <Grid>
                    <Button
                      disabled={isUpdate2 || isUpload || isDeleting}
                      onClick={this.updateProfile} color="primary">
                      Lưu
                    </Button>
                    <Button
                      disabled={isUpdate2 || isUpload || isDeleting}
                      style={{ marginLeft: 20 }}
                      onClick={() => this.setState({ isUpdate: !isUpdate })}
                      color="primary"
                    >
                      Quay lại
                    </Button>
                  </Grid>
                ) : (
                    <Grid>
                      <Button
                        disabled={isUpdate2 || isUpload || isDeleting}
                        onClick={this.changeForm} color="primary">
                        Cập nhật thông tin
                    </Button>
                      {showUpdateImage ? (
                        <Button
                          disabled={isUpdate2 || isUpload || isDeleting}
                          style={{ marginLeft: 20 }}
                          onClick={() =>
                            this.setState({ showUpdateImage: !showUpdateImage })
                          }
                          color="primary"
                        >
                          Huỷ cập nhật ảnh
                      </Button>
                      ) : (
                          <Button
                            disabled={isUpdate2 || isUpload || isDeleting}
                            style={{ marginLeft: 20 }}
                            onClick={() =>
                              this.setState({ showUpdateImage: !showUpdateImage })
                            }
                            color="primary"
                          >
                            Cập nhật ảnh
                      </Button>
                        )}
                      <Button
                        disabled={isUpdate2 || isUpload || isDeleting}
                        style={{ marginLeft: 20 }}
                        onClick={this.delete}
                        color="primary"
                      >
                        Xoá tài khoản
                    </Button>
                    </Grid>
                  )}
              </CardFooter>
            </Card>
          </GridItem>
          {!isUpdate && (
            <GridItem xs={12} sm={12} md={4}>
              {profile && profile.full_name &&
                <Card profile>
                  <CardAvatar profile>
                    <a href="#pablo" onClick={e => e.preventDefault()}>
                      <img
                        src={profile.picture ? `${ANOTHER_HOST}${profile.picture}` : 'http://www.stickpng.com/assets/images/585e4bf3cb11b227491c339a.png'}
                        alt="..."
                      />
                    </a>
                  </CardAvatar>
                  <CardBody>
                    <h6 className={classes.cardCategory}>{profile.full_name}</h6>
                    <Grid
                      style={{
                        display: "flex",
                        alignItems: "flex-start",
                        flexDirection: "column"
                      }}
                    >
                      <h7>{`Sđt : ${profile.phone}`}</h7>
                      <h7>{`Quyền : ${profile.role}`}</h7>
                      <h7>{`Email : ${profile.email}`}</h7>
                    </Grid>
                  </CardBody>
                </Card>}
            </GridItem>
          )}
        </GridContainer>
        <MySnackBar ref={"MySnackBar"} />
        <MyDialog
          ref={"MyDialog"}
          delete={() => onDeleteUser(profile.id, accessToken)}
        />
      </div>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  },
  menu: {
    width: 200
  }
};

const mapStateToProps = state => {
  return {
    profile: state.profileReducer.profile,
    accessToken: state.profileReducer.accessToken,

    isUpdate2: state.profileReducer.isUpdate,
    updateSuccess: state.profileReducer.updateSuccess,
    errorUpdate: state.profileReducer.errorUpdate,

    isDeleting: state.userDetailReducer.isDeleting,
    deleteSuccess: state.userDetailReducer.deleteSuccess,
    errorDelete: state.userDetailReducer.errorDelete,

    isUpload: state.profileReducer.isUpload,
    uploadSuccess: state.profileReducer.uploadSuccess,
    uploadError: state.profileReducer.uploadError,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetUserDetail: (id, token) => {
      dispatch(getUserDetail(id, token));
    },
    onUpdateUser: (id, body, token) => {
      dispatch(updateUser(id, body, token));
    },
    onDeleteUser: (id, token) => {
      dispatch(deleteUser(id, token));
    },
    onUploadImage: (id, body, token) => {
      dispatch(uploadImage(id, body, token));
    },
    onRemove: () => {
      dispatch(removeNow());
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(UserProfileManager);

export default withStyles(styles)(redux);
