import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from "react-redux";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";

import avatar from "assets/img/faces/marc.jpg";
import {
  getUserDetail,
  updateUser,
  deleteUser,
  removeNow,
  uploadImage,
  uploadImageUser
} from "../../../store/actions/detailUserAction";
import { TextField, Grid, MenuItem } from "@material-ui/core";
import Loading from "../../../components/Loading";
import ManagerHotels from "./ManagerHotels";
import { MANAGER } from "../../../config/constAct";
import MySnackBar from "../../../components/SnackBar";
import MyDialog from "../../../components/Dialog";
import { ANOTHER_HOST } from "../../../config/constAct";

const roles = [
  {
    label: "MANAGER"
  },
  {
    label: "CLIENT"
  }
];

class UserDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.history.location.state.id,
      isUpdate: false,
      username: "",
      name: "",
      full_name: "",
      role: "",
      email: "",
      phone: "",
      showUpdateImage: false
    };
  }
  componentDidMount() {
    this.props.onGetUserDetail(this.state.id, this.props.accessToken);
  }

  componentWillUnmount() {
    this.props.onRemove();
  }

  updateProfile = () => {
    const { userDetail, accessToken } = this.props;
    this.setState({ isUpdate: !this.state.isUpdate });
    this.props.onUpdateUser(userDetail.id, this.bodyUpdate(), accessToken);
  };

  bodyUpdate = () => {
    const { full_name, email, phone, name } = this.state;
    const body = {
      email: email,
      full_name: full_name,
      name: name,
      phone: phone
    };

    return JSON.stringify(body);
  };

  changeForm = () => {
    const { userDetail } = this.props;
    const { isUpdate } = this.state;
    this.setState({
      username: userDetail.username,
      name: userDetail.name,
      full_name: userDetail.full_name,
      email: userDetail.email,
      phone: userDetail.phone,
      isUpdate: !isUpdate
    });
  };

  componentWillReceiveProps(nextProps) {
    if (
      nextProps &&
      !!!nextProps.isUpdate2 &&
      !!nextProps.updateSuccess &&
      !!!nextProps.errorUpdate
    ) {
      this.openSnackBar("success", "Cập nhật thành công");
      this.props.onRemove();
      // this.onBack();
    }
    if (
      nextProps &&
      !!!nextProps.isUpdate2 &&
      !!!nextProps.updateSuccess &&
      nextProps.errorUpdate
    ) {
      this.openSnackBar(
        "error",
        `Cập nhật thất bại ! ${nextProps.errorUpdate}`
      );
      this.props.onRemove();
    }

    if (
      nextProps &&
      !!!nextProps.isDeleting &&
      !!nextProps.deleteSuccess &&
      !!!nextProps.errorDelete
    ) {
      console.log("xoaaaa");
      this.openSnackBar("success", "Xoá tài khoản thành công");
      setTimeout(() => {
        this.props.history.goBack();
      }, 500);
    }
    if (
      nextProps &&
      !!!nextProps.isDeleting &&
      !!!nextProps.deleteSuccess &&
      nextProps.errorDelete
    ) {
      this.openSnackBar(
        "error",
        `Xoá tài khoản thất bại ! ${nextProps.errorDelete}`
      );
      this.props.onRemove();
    }

    if (
      nextProps &&
      !!!nextProps.isUpload &&
      !!nextProps.uploadSuccess &&
      !!!nextProps.uploadError
    ) {
      this.openSnackBar("success", "Cập nhật ảnh thành công");
      this.setState({ showUpdateImage: !this.state.showUpdateImage });
      this.props.onRemove();
    }
    if (
      nextProps &&
      !!!nextProps.isUpload &&
      !!!nextProps.uploadSuccess &&
      nextProps.uploadError
    ) {
      this.openSnackBar(
        "error",
        `Cập nhật ảnh thất bại ! ${nextProps.uploadError}`
      );
      this.props.onRemove();
    }
  }

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  delete = () => {
    this.refs.MyDialog.handleClickOpen(
      "Xoá tài khoản",
      "Bạn có chắc muốn xoá tài khoản này ",
      false,
      true
    );
  };

  handleChange = event => {
    this.setState({ role: event.target.value });
  };

  updateImage = () => {
    const { file } = this.state;
    const { userDetail, accessToken } = this.props;
    if (!file) {
      alert("Vui lòng chọn ảnh !");
      return;
    }
    if (file.size > 3000000) {
      alert("Vui lòng chọn ảnh có dung lượng dưới 1MB");
      return;
    }
    var formData = new FormData();
    formData.append("file", file);

    this.props.onUploadImage(userDetail.id, formData, accessToken);
  };

  render() {
    const {
      classes,
      userDetail,
      isGetUserDetail,
      history,
      accessToken,
      onDeleteUser,
      isUpdate2,
      isUpload,
      isDeleting
    } = this.props;
    const {
      isUpdate,
      username,
      full_name,
      email,
      phone,
      name,
      showUpdateImage
    } = this.state;
    return (
      <div>
        <GridContainer>
          <GridItem xs={12} sm={12} md={isUpdate ? 12 : 8}>
            <Card>
              <CardHeader color="primary">
                <h4 className={classes.cardTitleWhite}>
                  {isUpdate ? `Chỉnh sửa thông tin` : "Thông tin"}
                </h4>
                <p className={classes.cardCategoryWhite}>
                  Complete your profile
                </p>
              </CardHeader>
              {isGetUserDetail ? (
                <Loading />
              ) : (
                <CardBody>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={8}>
                      <TextField
                        disabled={true}
                        id="standard-name"
                        label="Tài khoản "
                        value={isUpdate ? username : userDetail.username}
                        onChange={event =>
                          this.setState({ username: event.target.value })
                        }
                        margin="normal"
                        fullWidth
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={4}>
                      <TextField
                        id="standard-select-currency"
                        select
                        label="Chọn quyền"
                        disabled={true}
                        value={userDetail.role}
                        onChange={this.handleChange}
                        SelectProps={{
                          MenuProps: {
                            className: classes.menu
                          }
                        }}
                        margin="normal"
                        fullWidth
                      >
                        {roles.map(option => (
                          <MenuItem key={option.label} value={option.label}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </TextField>
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <TextField
                        disabled={!isUpdate}
                        id="standard-name"
                        label="Tên"
                        value={isUpdate ? name : userDetail.name}
                        onChange={event =>
                          this.setState({ name: event.target.value })
                        }
                        margin="normal"
                        fullWidth
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <TextField
                        disabled={!isUpdate}
                        id="standard-name"
                        label="Tên đầy đủ"
                        value={isUpdate ? full_name : userDetail.full_name}
                        onChange={event =>
                          this.setState({ full_name: event.target.value })
                        }
                        margin="normal"
                        fullWidth
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <TextField
                        disabled={!isUpdate}
                        id="standard-name"
                        label="Email"
                        value={isUpdate ? email : userDetail.email}
                        onChange={event =>
                          this.setState({ email: event.target.value })
                        }
                        margin="normal"
                        fullWidth
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <TextField
                        disabled={!isUpdate}
                        id="standard-name"
                        label="Số điện thoại"
                        value={isUpdate ? phone : userDetail.phone}
                        onChange={event =>
                          this.setState({ phone: event.target.value })
                        }
                        margin="normal"
                        fullWidth
                      />
                    </GridItem>
                  </GridContainer>
                  {showUpdateImage && (
                    <GridContainer>
                      <GridItem xs={12} sm={12} md={4}>
                        <h3
                          style={{
                            fontSize: 16,
                            marginTop: 15,
                            marginBottom: 15,
                            padding: 0
                          }}
                        >
                          {`Cập nhật ảnh đại diện `}
                        </h3>
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <input
                          style={{
                            fontSize: 16,
                            marginTop: 15,
                            marginBottom: 15,
                            padding: 0
                          }}
                          type="file"
                          onChange={event =>
                            this.setState({ file: event.target.files[0] })
                          }
                        />
                      </GridItem>
                      <GridItem xs={12} sm={12} md={4}>
                        <Button
                          //   disabled={
                          //     isUpdating ||
                          //     isChangeStatus ||
                          //     isLoadingHotel ||
                          //     isUpload
                          //   }
                          color="primary"
                          onClick={() => this.updateImage()}
                        >
                          Đăng ảnh
                        </Button>
                      </GridItem>
                    </GridContainer>
                  )}
                </CardBody>
              )}
              <CardFooter>
                {isUpdate ? (
                  <Grid>
                    <Button
                      disabled={
                        isUpdate2 || isUpload || isDeleting || isGetUserDetail
                      }
                      onClick={this.updateProfile}
                      color="primary"
                    >
                      Lưu
                    </Button>
                    <Button
                      disabled={
                        isUpdate2 || isUpload || isDeleting || isGetUserDetail
                      }
                      style={{ marginLeft: 20 }}
                      onClick={() => this.setState({ isUpdate: !isUpdate })}
                      color="primary"
                    >
                      Quay lại
                    </Button>
                  </Grid>
                ) : (
                  <Grid>
                    <Button
                      disabled={
                        isUpdate2 || isUpload || isDeleting || isGetUserDetail
                      }
                      onClick={this.changeForm}
                      color="primary"
                    >
                      Cập nhật thông tin
                    </Button>
                    {/* {showUpdateImage ? (
                      <Button
                        style={{ marginLeft: 20 }}
                        onClick={() =>
                          this.setState({ showUpdateImage: !showUpdateImage })
                        }
                        color="primary"
                      >
                        Huỷ cập nhật ảnh
                      </Button>
                    ) : (
                      <Button
                        style={{ marginLeft: 20 }}
                        onClick={() =>
                          this.setState({ showUpdateImage: !showUpdateImage })
                        }
                        color="primary"
                      >
                        Cập nhật ảnh
                      </Button>
                    )} */}
                    <Button
                      disabled={
                        isUpdate2 || isUpload || isDeleting || isGetUserDetail
                      }
                      style={{ marginLeft: 20 }}
                      onClick={this.delete}
                      color="primary"
                    >
                      Xoá tài khoản
                    </Button>
                  </Grid>
                )}
              </CardFooter>
            </Card>
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            {userDetail && userDetail.full_name && (
              <Card profile>
                <CardAvatar profile>
                  <a href="#pablo" onClick={e => e.preventDefault()}>
                    <img
                      src={
                        // userDetail.picture
                          // ?
                           `${ANOTHER_HOST}${userDetail.picture}`
                          // : "http://www.stickpng.com/assets/images/585e4bf3cb11b227491c339a.png"
                      }
                      alt="..."
                    />
                  </a>
                </CardAvatar>
                <CardBody>
                  <h6 className={classes.cardCategory}>
                    {userDetail.full_name}
                  </h6>
                  <Grid
                    style={{
                      display: "flex",
                      alignItems: "flex-start",
                      flexDirection: "column"
                    }}
                  >
                    <h7>{`Sđt : ${userDetail.phone}`}</h7>
                    <h7>{`Quyền : ${userDetail.role}`}</h7>
                    <h7>{`Email : ${userDetail.email}`}</h7>
                  </Grid>
                </CardBody>
              </Card>
            )}
          </GridItem>

          {userDetail &&
            userDetail.id &&
            history.location.state.type === MANAGER && (
              <GridItem xs={12} sm={12} md={12}>
                <ManagerHotels
                  {...this.props}
                  id={userDetail.id}
                  classes={classes}
                />
              </GridItem>
            )}
        </GridContainer>
        <MySnackBar ref={"MySnackBar"} />
        <MyDialog
          ref={"MyDialog"}
          delete={() => onDeleteUser(userDetail.id, accessToken)}
        />
      </div>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  },
  menu: {
    width: 200
  }
};

const mapStateToProps = state => {
  return {
    profile: state.profileReducer.profile,
    accessToken: state.profileReducer.accessToken,
    userDetail: state.userDetailReducer.userDetail,
    isGetUserDetail: state.userDetailReducer.isGetUserDetail,
    getDetailSuccess: state.userDetailReducer.getDetailSuccess,
    errorGetDetail: state.userDetailReducer.errorGetDetail,

    isUpdate2: state.userDetailReducer.isUpdate,
    updateSuccess: state.userDetailReducer.updateSuccess,
    errorUpdate: state.userDetailReducer.errorUpdate,

    isDeleting: state.userDetailReducer.isDeleting,
    deleteSuccess: state.userDetailReducer.deleteSuccess,
    errorDelete: state.userDetailReducer.errorDelete,

    isUpload: state.userDetailReducer.isUpload,
    uploadSuccess: state.userDetailReducer.uploadSuccess,
    uploadError: state.userDetailReducer.uploadError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onGetUserDetail: (id, token) => {
      dispatch(getUserDetail(id, token));
    },
    onUpdateUser: (id, body, token) => {
      dispatch(updateUser(id, body, token));
    },
    onDeleteUser: (id, token) => {
      dispatch(deleteUser(id, token));
    },
    onUploadImage: (id, body, token) => {
      dispatch(uploadImageUser(id, body, token));
    },
    onRemove: () => {
      dispatch(removeNow());
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(UserDetail);

export default withStyles(styles)(redux);
