import {
  GET_LIST_BOOKING_REQUEST,
  GET_LIST_BOOKING_SUCCESS,
  GET_LIST_BOOKING_FAILED,
  LOG_OUT,
} from "../../config/constAct";

const initialState = {
  isGetList: false,
  getListSuccess: false,
  getListError: null,
  list: []
};

const bookingReducer = (state = initialState, action) => {
  switch (action.type) {
    //GET LIST
    case GET_LIST_BOOKING_REQUEST:
      return Object.assign({}, state, {
        isGetList: true,
        getListSuccess: false,
        getListError: null,
        list: []
      });

    case GET_LIST_BOOKING_SUCCESS:
      return Object.assign({}, state, {
        isGetList: false,
        getListSuccess: true,
        getListError: null,
        list: action.payload
      });

    case GET_LIST_BOOKING_FAILED:
      return Object.assign({}, state, {
        isGetList: false,
        getListSuccess: false,
        getListError: action.payload,
        list: []
      });
    case LOG_OUT:
      return initialState;
    default:
      return state;
  }
};

export default bookingReducer;
