import React, { Component } from 'react';
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from 'react-redux'
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { TextField, Grid, MenuItem } from "@material-ui/core";
import { createUser } from '../../../store/actions/detailUserAction';
import MySnackBar from '../../../components/SnackBar';

const USER_NAME_REGEX = /^(?=.{8,100}$)(?![_.])(?!.*[_.]{2})[a-zA-Z0-9._]+(?<![_.])$/
const PHONE_REGEX = /^\(?([0-9]{3})\)?[-.\s]?([0-9]{3})[-.\s]?([0-9]{4})$/

const roles = [
    {
        label: 'MANAGER',
    },
    {
        label: 'CLIENT',
    },
];
class CreateUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            full_name: "",
            role: "",
            email: "",
            phone: "",
            name: "",
            password: ""
        };
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps && !!!nextProps.isCreating && !!nextProps.createSuccess && !!!nextProps.errorCreate) {
            this.openSnackBar("success", "Tạo mới thành công");
            setTimeout(() => {
                this.onBack();
            }, 1000);
        }
        if (nextProps && !!!nextProps.isCreating && !!!nextProps.createSuccess && nextProps.errorCreate) {
            this.openSnackBar("error", `Cập nhật thất bại ! ${nextProps.errorCreate}`);
        }
    }

    onBack = () => {
        this.props.history.goBack();
    };

    create = () => {
        const { username, full_name, role, email, phone, name, password } = this.state
        const { accessToken } = this.props
        if (username !== '' && full_name !== '' && role !== '' && email !== '' && phone !== '' && name !== '' && password !== '') {
            this.props.onCreateUser(this.makeBody(), accessToken, role === 'MANAGER' ? 'managers' : 'clients')
        } else {
            this.openSnackBar("warning", "Vui lòng điền đầy đủ thông tin");
        }
    }

    makeBody = () => {
        const { username, full_name, role, email, phone, name, password } = this.state
        const body = {
            email: email,
            full_name: full_name,
            name: name,
            password: password,
            phone: phone,
            picture: "https://www.biography.com/.image/ar_1:1%2Cc_fill%2Ccs_srgb%2Cg_face%2Cq_auto:good%2Cw_300/MTE4MDAzNDEwNzg5ODI4MTEw/barack-obama-12782369-1-402.jpg",
            role: role,
            username: username
        }

        console.log('body', JSON.stringify(body))

        return JSON.stringify(body)
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };

    render() {
        const { classes } = this.props;
        const { username, full_name, role, email, phone, name, password, errorUsername, errorPhone } = this.state
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="primary">
                            <h4 className={classes.cardTitleWhite}>Tạp user mới</h4>
                            <p className={classes.cardCategoryWhite}>Complete your profile</p>
                        </CardHeader>
                        <CardBody>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Tài khoản "
                                        value={username}
                                        error={errorUsername}
                                        onChange={event => {
                                            if (!USER_NAME_REGEX.test(event.target.value)) {
                                                this.setState({
                                                    username: event.target.value,
                                                    errorUsername: "sai rồi "
                                                });
                                            } else {
                                                this.setState({ username: event.target.value, errorUsername: null });
                                            }
                                        }
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Mật khẩu"
                                        value={password}
                                        onChange={event =>
                                            this.setState({ password: event.target.value })
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>
                            </GridContainer>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-select-currency"
                                        select
                                        label="Chọn quyền"
                                        value={role}
                                        onChange={this.handleChange}
                                        SelectProps={{
                                            MenuProps: {
                                                className: classes.menu,
                                            },
                                        }}
                                        margin="normal"
                                        fullWidth
                                    >
                                        {roles.map(option => (
                                            <MenuItem key={option.label} value={option.label}>
                                                {option.label}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </GridItem>
                            </GridContainer>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Tên"
                                        value={name}
                                        onChange={event =>
                                            this.setState({ name: event.target.value })
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Tên đầy đủ"
                                        value={full_name}
                                        onChange={event =>
                                            this.setState({ full_name: event.target.value })
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>

                            </GridContainer>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Email"
                                        value={email}
                                        onChange={event =>
                                            this.setState({ email: event.target.value })
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <TextField
                                        id="standard-name"
                                        label="Số điện thoại"
                                        value={phone}
                                        error={errorPhone}
                                        onChange={event => {
                                            if (!PHONE_REGEX.test(event.target.value)) {
                                                this.setState({
                                                    phone: event.target.value,
                                                    errorPhone: "sai rồi "
                                                });
                                            } else {
                                                this.setState({ phone: event.target.value, errorPhone: null });
                                            }
                                        }
                                        }
                                        margin="normal"
                                        fullWidth
                                    />
                                </GridItem>
                            </GridContainer>
                        </CardBody>
                        <CardFooter>
                            <Button onClick={this.create} color="primary">Tạo mới</Button>
                        </CardFooter>
                    </Card>
                </GridItem>
                <MySnackBar ref={"MySnackBar"} />
            </GridContainer>
        );
    }

}

const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none"
    },
    menu: {
        width: 200
    }
};

const mapStateToProps = state => {
    return {
        accessToken: state.profileReducer.accessToken,
        isCreating: state.userDetailReducer.isCreating,
        createSuccess: state.userDetailReducer.createSuccess,
        errorCreate: state.userDetailReducer.errorCreate
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onCreateUser: (body, token, type) => {
            dispatch(createUser(body, token, type))
        }
    }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(CreateUser)

export default withStyles(styles)(redux);