import {
    GET_LIST_HOTEL_REQUEST,
    GET_LIST_HOTEL_FAILED,
    GET_LIST_HOTEL_SUCCESS,
    HOST,
    DELETE_HOTEL_REQUEST,
    DELETE_HOTEL_SUCCESS,
    DELETE_HOTEL_FAILED,
    SEARCH_REQUEST,
    SEARCH_SUCCESS,
    SEARCH_FAILED
} from "../../config/constAct";

//GET LIST
export const getListHotelRequest = () => ({
    type: GET_LIST_HOTEL_REQUEST
})

export const getListHotelSuccess = (data) => ({
    type: GET_LIST_HOTEL_SUCCESS,
    payload: data
})

export const getListHotelFailed = (error) => ({
    type: GET_LIST_HOTEL_FAILED,
    payload: error
})

export const getListHotel = page => {
    return async dispatch => {
        dispatch(getListHotelRequest())
        try {
            let config = {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                },
            }

            let response = await fetch(`${HOST}/hotels?page_size=10&page=${page}`, config)
            let json = await response.json()
            console.log('get list hotel', json)
            if (json.content) {
                dispatch(getListHotelSuccess(json))
            } else {
                dispatch(getListHotelFailed(json.message))
            }
        } catch (error) {
            console.log('error', error)
            dispatch(getListHotelFailed(error))
        }
    }
}

//DELETE
export const deleteHotelRequest = () => ({
    type: DELETE_HOTEL_REQUEST
})

export const deleteHotelSuccess = (data) => ({
    type: DELETE_HOTEL_SUCCESS,
    payload: data
})

export const deleteHotelFailed = (error) => ({
    type: DELETE_HOTEL_FAILED,
    payload: error
})


export const deleteHotel = (token,id) => {
    return async dispatch => {
        dispatch(deleteHotelRequest(id))
        try {
            let config = {
                method: 'DELETE',
                headers: {
                    'Accept': 'application/json',
                    Authorization: "Bearer " + token
                },
            }

            let response = await fetch(`${HOST}/hotels/${id}`, config)
            let json = await response.json()
            console.log('delete hotel', json)
            if(json.status && json.status === true){
                dispatch(deleteHotelSuccess(json.hotel.id))
            } else {
                dispatch(deleteHotelFailed(json.message))
            }

        } catch (error) {
            console.log('error', error)
            dispatch(deleteHotelFailed(error))
        }
    }
}


export const searchHotelByNameRequest = () => ({
    type: SEARCH_REQUEST
})

export const searchHotelByNameSuccess = (data) => ({
    type: SEARCH_SUCCESS,
    payload: data
})

export const searchHotelByNameFailed = (error) => ({
    type: SEARCH_FAILED,
    payload: error
})


export const searchHotelByName = (name) => {
    return async dispatch => {
        dispatch(getListHotelRequest())
        try {
            let config = {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    "Content-Type": "application/json",
                },
            }
            let response = await fetch(`${HOST}/hotels?name=${name}`, config)
            let json = await response.json()
            console.log('searchByName', json)
            if (json.content) {
                dispatch(getListHotelSuccess(json))
            } else {
                dispatch(getListHotelFailed(json.message))
            }

        } catch (error) {
            console.log('error', error)
            dispatch(getListHotelFailed(error))
        }
    }
}
