import React, { Component } from 'react';
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import KeyboardArrowDown from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUp from "@material-ui/icons/KeyboardArrowUp";
import TableRoom from '../../../../components/Table/TableRoom';
import {
    Grid,
} from "@material-ui/core";

export default class RoomDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showContent: false
        };
    }

    render() {
        const { showContent } = this.state;
        const { classes, item } = this.props
        return (
            <GridItem xs={12} sm={12} md={12}>
                <Card>
                    <CardHeader
                        color="primary"
                        onClick={() => this.setState({ showContent: !showContent })}
                    >
                        <Grid
                            container
                            style={{
                                flex: 1,
                                flexDirection: "row",
                                justifyContent: "space-between"
                            }}
                        >
                            <h4 className={classes.cardTitleWhite}>{item.name}</h4>
                            {showContent ? (
                                <KeyboardArrowUp />
                            ) : (
                                    <KeyboardArrowDown />
                                )}
                        </Grid>
                    </CardHeader>
                    <CardBody>
                        {showContent && <TableRoom
                            onSelectTable={this.onSelectTable}
                            tableHeaderColor="primary"
                            tableHead={["Name", "Price"]}
                            tableData={item.prices}
                        />}
                    </CardBody>
                </Card>
            </GridItem>
        );
    }
}
