import { combineReducers } from "redux";
import listHotelReducer from "./reducers/listHotelReducer";
import detailHotelReducer from "./reducers/detailHotelReducer";
import profileReducer from "./reducers/profileReducer";
import userDetailReducer from './reducers/userDetailReducer'
import bookingReducer from './reducers/bookingReducer'
import verifyReducer from './reducers/verifyReducer'

const rootReducer = combineReducers({
  listHotelReducer,
  detailHotelReducer,
  profileReducer,
  userDetailReducer,
  bookingReducer,
  verifyReducer
});

export default rootReducer;
