import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import { connect } from 'react-redux';
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { getHotelManager, addHotelManager, deleteHotelManager, removeCache } from "../../../store/actions/detailUserAction";
import Loading from '../../../components/Loading'
import TableHotel from "../../../components/Table/TableHotel";
import { Grid } from '@material-ui/core'
import ListDialog from "./ListDialog";
import MySnackBar from "../../../components/SnackBar";
import CustomTable from "../../../components/CustomTable";
import MyDialog from "../../../components/Dialog";
import { MANAGER } from "../../../config/constAct";
class ManagerHotels extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isDelete: false,
            currentPage: 0,
            item: null,
            isFilter: false,
            selected: []
        };
        this.listDialog = React.createRef()
    }

    componentDidMount() {
        console.log('didmount')
        this.get()
    }

    get = () => {
        const { accessToken, id, profile } = this.props
        if (id) {
            this.props.onGetHotelManager(id, accessToken)
        }
        if (profile.role === MANAGER) {
            this.props.onGetHotelManager(profile.id, accessToken)
        }
    }

    onSelectTable = detail => {
        console.log('detail', detail)
        this.navigate("/main/listhotel/detail", detail.hotel_id);
    };

    navigate = (name, param) => {
        this.props.history.push({
            pathname: name,
            state: {
                id: param
            }
        });
    };

    addHotelManager = () => {
        const { profile } = this.props
        if (profile.role === MANAGER) {
            this.navigate("/main/listhotel/create");
        } else {
            this.listDialog && this.listDialog.current && this.listDialog.current.handleClickOpen()
        }
    }

    callApi = (id) => {
        const body = {
            hotel_id: id
        }
        const { accessToken } = this.props
        this.props.onAddHotelManager(this.props.id, JSON.stringify(body), accessToken)
    }

    componentWillReceiveProps(nextProps) {
        //ADD HOTEL LISTENER
        if (nextProps && !!!nextProps.isAddHotelManager && !!nextProps.addHotelManagerSuccess && !!!nextProps.errorAdd) {
            this.openSnackBar("success", "Thêm thành công");
            this.props.onRemove()
        }
        if (nextProps && !!!nextProps.isAddHotelManager && !!nextProps.addHotelManagerSuccess && !!!nextProps.errorAdd && !!nextProps.done) {
            this.get()
            this.props.onRemove()
        }
        if (nextProps && !!!nextProps.isAddHotelManager && !!!nextProps.addHotelManagerSuccess && nextProps.errorAdd) {
            this.openSnackBar("error", `Thêm thất bại ! ${nextProps.errorAdd}`);
            this.props.onRemove()
        }

        //GET HOTEL LISTENER
        if (nextProps && !!!nextProps.isGetHotelManager && !!!nextProps.getHotelManagerSuccess && nextProps.errorGetHotelManager) {
            this.openSnackBar("error", `Lấy danh sách khách sạn thất bại ! ${nextProps.errorGetHotelManager}`);
            this.props.onRemove()
        }

        //DELETE HOTEL LISTENER
        if (nextProps && !!!nextProps.idDeleteHotel && !!nextProps.deleteHotelSuccess && !!!nextProps.deletehotelError) {
            this.openSnackBar("success", "Xoá thành công");
            this.props.onRemove()
        }

        if (nextProps && !!!nextProps.idDeleteHotel && !!nextProps.deleteHotelSuccess && !!!nextProps.deletehotelError && !!nextProps.done) {
            this.get()
            this.props.onRemove()
        }
        if (nextProps && !!!nextProps.idDeleteHotel && !!!nextProps.deleteHotelSuccess && nextProps.deletehotelError) {
            this.openSnackBar("error", `Xoá thất bại ! ${nextProps.deletehotelError}`);
            this.props.onRemove()
        }
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };

    deleteHotelManger = () => {
        this.refs.MyDialog.handleClickOpen("Xoá", "Bạn có chắc muốn xoá khách sạn này khỏi danh sách quản lý ?", false, true)
        // this.setState({ isDelete: !this.state.isDelete })
    }

    excuteData = (item, type) => {
        this.setState({ item })
        if (type === 'add') {
            this.refs.MyDialog.handleClickOpen("Xoá", "Bạn có chắc muốn xoá khách sạn này khỏi danh sách quản lý ?", false, true)
        }
    }

    delete = () => {
        const { accessToken, id } = this.props
        console.log('this.state.selected', this.state.selected)
        this.props.onDeleteHotelManager(id, this.state.selected[0], accessToken)
        this.setState({ isFilter: false })
    }

    getSelectedList = (list) => {
        this.setState({ selected: list });
    }

    render() {
        const { isDelete, isFilter } = this.state
        const { classes, isGetHotelManager, hotelManager, profile } = this.props
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="primary">
                            <h4 className={classes.cardTitleWhite}>Các khách sạn quản lý </h4>
                        </CardHeader>
                        <CardBody>
                            {isGetHotelManager ? <Loading />
                                : <Grid>
                                    {hotelManager && hotelManager.length !== 0 && !isFilter
                                        && <TableHotel
                                            isManager
                                            onSelectTable={this.onSelectTable}
                                            tableHeaderColor="primary"
                                            tableHead={[
                                                "Tên ",
                                                "Địa chỉ",
                                                "Lượt đánh giá",
                                                "Điểm",
                                                "Mô tả"
                                            ]}
                                            onPrevious={this.onPrevious}
                                            onNext={this.onNext}
                                        />
                                    }
                                    {hotelManager && hotelManager.length !== 0 && isFilter
                                        && <CustomTable
                                            isManager
                                            callBackList={this.getSelectedList}
                                            onPrevious={this.onPrevious}
                                            onNext={this.onNext}
                                            rows={[
                                                {
                                                    id: "name",
                                                    label: "Tên"
                                                },
                                                {
                                                    id: "address",
                                                    label: "Địa chỉ"
                                                },
                                                {
                                                    id: "ratings",
                                                    label: "Lượt đánh giá"
                                                },
                                                {
                                                    id: "point",
                                                    label: "Điểm"
                                                },
                                                {
                                                    id: "description",
                                                    label: "Mô tả"
                                                }
                                            ]}
                                        />

                                    }
                                    {hotelManager && hotelManager.length === 0
                                        && <Grid style={{ display: 'flex', justifyContent: 'center' }}>
                                            <h1 style={{ fontSize: 18 }}>User này chưa quản lý khách sạn nào </h1>
                                        </Grid>}

                                </Grid>}
                        </CardBody>
                        <CardFooter>
                            <Grid container direction="row">
                                <Button onClick={this.addHotelManager} color="primary">{profile.role === MANAGER ? "Tạo mới" : "Thêm khách sạn quản lý"}</Button>
                                {isFilter ?
                                    <Grid>
                                        <Button onClick={this.deleteHotelManger} style={{ marginLeft: 20, marginRight: 20 }} color="primary">Xoá</Button>
                                        <Button onClick={() => this.setState({ isFilter: !isFilter })} color="primary">Huỷ</Button>
                                    </Grid>
                                    : <Button onClick={() => this.setState({ isFilter: !isFilter })} style={{ marginLeft: 20 }} color="primary">Xoá khách sạn quản lý</Button>}
                            </Grid>
                        </CardFooter>
                    </Card>
                </GridItem>
                <ListDialog ref={this.listDialog} callBackId={(id) => this.callApi(id)} />
                <MySnackBar ref={"MySnackBar"} />
                <MyDialog ref={"MyDialog"} delete={this.delete} />
            </GridContainer>
        );
    }
}

const mapStateToProps = state => {
    return {
        accessToken: state.profileReducer.accessToken,
        hotelManager: state.userDetailReducer.hotelManager,
        isGetHotelManager: state.userDetailReducer.isGetHotelManager,
        getHotelManagerSuccess: state.userDetailReducer.getHotelManagerSuccess,
        errorGetHotelManager: state.userDetailReducer.errorGetHotelManager,
        done: state.userDetailReducer.done,

        isAddHotelManager: state.userDetailReducer.isAddHotelManager,
        addHotelManagerSuccess: state.userDetailReducer.addHotelManagerSuccess,
        errorAdd: state.userDetailReducer.errorAdd,

        idDeleteHotel: state.userDetailReducer.idDeleteHotel,
        deleteHotelSuccess: state.userDetailReducer.deleteHotelSuccess,
        deletehotelError: state.userDetailReducer.deletehotelError,

        profile: state.profileReducer.profile
    }
}

const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none"
    },
    menu: {
        width: 200
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onGetHotelManager: (id, token) => {
            dispatch(getHotelManager(id, token))
        },
        onAddHotelManager: (id, body, token) => {
            dispatch(addHotelManager(id, body, token))
        },
        onDeleteHotelManager: (idManager, idHotel, token) => {
            dispatch(deleteHotelManager(idManager, idHotel, token))
        },
        onRemove :() => {
            dispatch(removeCache())
        }
    }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(ManagerHotels)

export default withStyles(styles)(redux)