import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILED,
  SIGNUP_REQUEST,
  SIGNUP_SUCCESS,
  SIGNUP_FAILED,
  LOG_OUT,
  UPLOAD_AVATAR_REQUEST,
  UPLOAD_AVATAR_SUCCESS,
  UPLOAD_AVATAR_FAILED,
  UPDATE_PROFILE_REQUEST,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_FAILED
} from "../../config/constAct";

const initialState = {
  profile: {},
  accessToken: "",
  role: "",

  isLoading: false,
  isSignup: false,
  loginSuccess: false,
  isLoggedIn: false,

  isUpload: false,
  uploadSuccess: false,
  uploadError: null,

  isUpdate: false,
  updateSuccess: false,
  errorUpdate: null
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    //LOGIN WITH ACCOUNT
    case LOGIN_REQUEST:
      return Object.assign({}, state, {
        isLoading: true,
        loginSuccess: false,
        errorLogin: ""
      });

    case LOGIN_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        profile: action.payload.user,
        accessToken: action.payload.access_token,
        role: action.payload.user.role,
        loginSuccess: true,
        isLoggedIn: true
      });

    case LOGIN_FAILED:
      return Object.assign({}, state, {
        isLoading: false,
        loginSuccess: false,
        errorLogin: action.payload
      });

    //SIGNUP
    case SIGNUP_REQUEST:
      return Object.assign({}, state, {
        isSignup: true
      });

    case SIGNUP_SUCCESS:
      return Object.assign({}, state, {
        isSignup: false,
        profile: action.payload
      });

    case SIGNUP_FAILED:
      return Object.assign({}, state, {
        isSignup: false
      });

    //UPLOAD IMAGE
    case UPLOAD_AVATAR_REQUEST:
      return Object.assign({}, state, {
        isUpload: true,
        uploadSuccess: false,
        uploadError: null
      });

    case UPLOAD_AVATAR_SUCCESS:
      return Object.assign({}, state, {
        isUpload: false,
        uploadSuccess: true,
        uploadError: null,
        profile: Object.assign({}, state.profile, {
          picture: action.payload
        })
      });

    case UPLOAD_AVATAR_FAILED:
      return Object.assign({}, state, {
        isUpload: false,
        uploadSuccess: false,
        uploadError: action.payload
      });

      //UPDATE_USER
    case UPDATE_PROFILE_REQUEST:
      return Object.assign({}, state, {
        isUpdate: true,
        updateSuccess: false,
        errorUpdate: null
      });
    case UPDATE_PROFILE_SUCCESS:
      return Object.assign({}, state, {
        isUpdate: false,
        updateSuccess: true,
        errorUpdate: null,
        profile: action.payload
      });
    case UPDATE_PROFILE_FAILED:
      return Object.assign({}, state, {
        isUpdate: false,
        updateSuccess: false,
        errorUpdate: action.payload
      });

    case LOG_OUT:
      return initialState;

    default:
      return state;
  }
};

export default profileReducer;
