import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import numeral from 'numeral'
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { Button, Checkbox } from "@material-ui/core";

function TableHotelManager({ ...props }) {
  const { classes, tableHead, tableData, tableHeaderColor, onSelectTable, isManager,isAdd, isChecked } = props;
  return (
    <div className={classes.tableResponsive}>
      <Table className={classes.table}>
        {tableHead !== undefined ? (
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow>
              {isChecked && <TableCell padding="checkbox">
                <h3 style={{ fontSize: 15 }}>Select</h3>
              </TableCell>}
              {tableHead.map((prop, key) => {
                return (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={key}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
        ) : null}
        <TableBody>
          {tableData.map((prop, key) => {
            return (
              <ItemRow key={key} isAdd={isAdd} prop={prop} isManager={isManager} isChecked={isChecked} classes={classes}
                checkItem={(item,type) => props.getItem(item,type)}
              />
            );
          })}
        </TableBody>
      </Table>
    </div>
  );
}

TableHotelManager.defaultProps = {
  tableHeaderColor: "gray"
};

TableHotelManager.propTypes = {
  classes: PropTypes.object.isRequired,
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string),
  tableData: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string))
};

export default withStyles(tableStyle)(TableHotelManager);

class ItemRow extends React.Component {
  state = {
    check: false
  }

  onChange = prop => () => {
    this.setState({ check: !this.state.check }, () => {
      if(this.state.check){
        this.props.checkItem(this.props.isAdd ? prop.id :prop.hotel_id,"add")
      } else {
        this.props.checkItem(this.props.isAdd ? prop.id :prop.hotel_id,"delete")
      }
    })
  }
  render() {
    const { check } = this.state
    const { key, isChecked, classes, isManager, prop } = this.props
    return (
      <TableRow role={'checkbox'} key={key} >
        {isChecked && <TableCell padding="checkbox">
          <Checkbox checked={check} onChange={this.onChange(prop)} />
        </TableCell>}
        <TableCell className={classes.tableCell}>
          {isManager ? prop.hotel.name : prop.name}
        </TableCell>
        <TableCell className={classes.tableCell}>
          {isManager ? prop.hotel.address : prop.address}
        </TableCell>
        <TableCell className={classes.tableCell}>
          {isManager ? prop.hotel.ratings : prop.ratings}
        </TableCell>
        <TableCell className={classes.tableCell}>
        {isManager ? (Math.floor(prop.hotel.point * 10) / 10) + "/5" : (Math.floor(prop.point * 10) / 10) + "/5"}
        </TableCell>
        <TableCell className={classes.tableCell}>
          {isManager ? prop.hotel.description : prop.description}
        </TableCell>
      </TableRow>
    )
  }
}
