import React, { Component } from "react";
import {
    Slide,
    DialogActions,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    Button,
    TextField,
    Grid
} from "@material-ui/core";
import { connect } from 'react-redux';
import { searchHotelByName } from "../../../store/actions/listHotelAction";
import { HOST } from "../../../config/constAct";
import TableHotelManager from "../../../components/Table/TableHotelManager";

function Transition(props) {
    return <Slide direction="up" {...props} />;
}

export default class ListDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            content: [],
            open: false,
            listId: [],
            isError: null
        };
    }

    handleClickOpen = () => {
        this.setState({
            open: true,
        });
    };

    handleClose = () => {
        this.setState({
            content: [],
            open: false,
            listId: [],
            isError: null
        });

    };

    handleCloseNormal = () => {
        this.setState({ open: false });
    };

    handleSearch = async (text) => {
        try {
            let config = {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    "Content-Type": "application/json",
                },
            }
            let response = await fetch(`${HOST}/hotels?name=${text}`, config)
            let json = await response.json()
            console.log('json',json)
            this.setState({ content: json.content })

        } catch (error) {
            console.log('error', error)
        }
    }

    handleData = () => {
        if (this.state.listId.length !== 1) {
            this.setState({ isError: true })
        } else {
            this.props.callBackId(this.state.listId[0])
            this.handleClose()
        }
    }

    excuteData = (item, type) => {
        const { listId } = this.state
        let arr = listId
        switch (type) {
            case "add":
                {
                    let a = arr.filter(i => i === item)
                    if (a.length === 0) {
                        console.log('item', item)
                        arr.push(item)
                        console.log('arr', arr)
                        this.setState({ listId: arr })
                    }
                }

                break;
            case "delete":
                {
                    let a = arr.filter(i => i === item)
                    if (a.length !== 0) {
                        let result = arr.filter(i => i !== item)
                        console.log('item', item)
                        console.log('result', result)
                        this.setState({ listId: result })
                    }
                }
                break;

            default:
                return;
        }
    }

    render() {
        const { name, content, isError } = this.state
        return (
            <div>
                <Dialog
                    open={this.state.open}
                    TransitionComponent={Transition}
                    keepMounted
                    onClose={this.handleCloseNormal}
                    // disableBackdropClick={true}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogContent>
                        <Grid>
                            <TextField
                                id="standard-name"
                                label="Tên khách sạn"
                                value={name}
                                onChange={event =>
                                    this.handleSearch(event.target.value)
                                }
                                margin="normal"
                                fullWidth
                            />
                        </Grid>
                        {content && content.length !== 0 ? <TableHotelManager
                            isChecked
                            isAdd
                            getItem={(item, type) => this.excuteData(item, type)}
                            onSelectTable={this.onSelectTable}
                            tableHeaderColor="primary"
                            tableHead={[
                                "Tên ",
                                "Địa chỉ",
                                "Lượt đánh giá",
                                "Điểm",
                                "Mô tả"
                            ]}
                            tableData={content}
                        />
                            : <Grid style={{ display: 'flex', justifyContent: 'center' }}>
                                <h1 style={{ fontSize: 15, color: 'black' }}>Tìm kiếm khách sạn muốn quản lý</h1>
                            </Grid>}
                    </DialogContent>
                    {isError && <Grid style={{ display: 'flex', justifyContent: 'center' }}>
                        <h1 style={{ fontSize: 15, color: 'red' }}>Vui lòng chọn 1 khách sạn</h1>
                    </Grid>}
                    <DialogActions>
                        <Button onClick={this.handleData} color="primary">
                            Đồng ý
            </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}