import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

import GridContainer from "components/Grid/GridContainer.js";

import { connect } from "react-redux";
import {
  removeData,
} from "../../../store/actions/detailHotelAction";

import InfoContainer from "./components/InfoContainer";
import RoomContainer from "./components/RoomContainer";
import ReviewContainer from "./components/ReviewContainer";

class DetailHotel extends React.Component {
  constructor(props) {
    super(props);
    console.log('abcdef',props.history.location.state.id,)
    this.state = {
      id: props.history.location.state.id,
    }
  }

  componentWillUnmount() {
    this.props.onRemove();
  }

  render() {
    const {
      classes,
      history
    } = this.props;
    const {
      id,
    } = this.state;
    return (
      <GridContainer>
        {/* Render detail */}
        <InfoContainer classes={classes} id={id} onBack={() => history.goBack()} />


        {/* Render room */}
        <RoomContainer classes={classes} id={id} />


        {/* Render review  */}
        <ReviewContainer classes={classes} id={id} />
      </GridContainer>
    );
  }
}

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};


const mapDispatchToProps = dispatch => {
  return {
    onRemove: () => {
      dispatch(removeData());
    }
  };
};

const redux = connect(
  null,
  mapDispatchToProps
)(DetailHotel);

export default withStyles(styles)(redux);
