import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Grid, IconButton, Checkbox, TableRow, TableCell, TableBody, Table } from "@material-ui/core";
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import MyCustomHead from "./MyCustomHead";
import { connect } from 'react-redux';
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";

function desc(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function stableSort(array, cmp) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = cmp(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map(el => el[0]);
}

function getSorting(order, orderBy) {
  return order === "desc"
    ? (a, b) => desc(a, b, orderBy)
    : (a, b) => -desc(a, b, orderBy);
}

class MyCustomTable extends React.Component {
  state = {
    order: "desc",
    orderBy: "name",
    selected: [],
  };

  handleRequestSort = (event, property) => {
    const orderBy = property;
    let order = "desc";

    if (this.state.orderBy === property && this.state.order === "desc") {
      order = "asc";
    }

    this.setState({ order, orderBy });
  };

  handleSelectAllClick = event => {
    const { listHotel, hotelManager, isManager } = this.props;
    if (event.target.checked) {
      this.setState({ selected: isManager ? hotelManager.map(n => n.hotel.id) : listHotel.map(n => n.id) });
      return;
    }
    this.setState({ selected: [] });
  };

  handleClick = (event, id) => {
    const { selected } = this.state;
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }

    this.setState({ selected: newSelected });
    this.props.callBackList(newSelected);
  };

  isSelected = id => this.state.selected.indexOf(id) !== -1;

  render() {
    const { classes, listHotel, rows, current_page, total_pages, isLoading, onPrevious, onNext, isManager, hotelManager, current_page_hm, total_pages_hm, isGetHotelManager } = this.props;
    const { order, orderBy, selected } = this.state;

    return (
      <div className={classes.tableResponsive}>
        <Table className={classes.table}>
          <MyCustomHead
            numSelected={selected.length}
            order={order}
            orderBy={orderBy}
            rows={rows}
            onSelectAllClick={this.handleSelectAllClick}
            onRequestSort={this.handleRequestSort}
            rowCount={isManager ? hotelManager.length : listHotel.length}
          />
          <TableBody>
            {stableSort(isManager ? hotelManager : listHotel, getSorting(order, orderBy))
              .map(n => {
                const isSelected = this.isSelected(isManager ? n.hotel.id : n.id);
                return (
                  <TableRow
                    hover
                    onClick={event => this.handleClick(event, isManager ? n.hotel.id : n.id)}
                    role="checkbox"
                    aria-checked={isSelected}
                    tabIndex={-1}
                    key={isManager ? n.hotel.id : n.id}
                    selected={isSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox checked={isSelected} />
                    </TableCell>
                    <TableCell
                      className={classes.tableCell}
                      component="th"
                      scope="row"
                      padding="none"
                    >
                      {isManager ? n.hotel.name : n.name}
                    </TableCell>
                    <TableCell className={classes.tableCell} align="left">
                      {isManager ? n.hotel.address :n.address}
                    </TableCell>
                    <TableCell className={classes.tableCell} align="left">
                      {isManager ? n.hotel.ratings :n.ratings}
                    </TableCell>
                    <TableCell className={classes.tableCell} align="left">
                      {isManager ? (Math.floor(n.hotel.point * 10) / 10) + "/5" :(Math.floor(n.point * 10) / 10) + "/5"}
                    </TableCell>
                    <TableCell className={classes.tableCell} align="left">
                      {isManager ? n.hotel.description :n.description}
                    </TableCell>
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
        {isManager
            ? <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
              <h4 style={{ fontSize: 14 }}>{`Trang ${current_page_hm + 1} / ${total_pages_hm}`}</h4>
              <IconButton disabled={current_page_hm === 0 || isGetHotelManager}>
                <KeyboardArrowLeft onClick={onPrevious} />
              </IconButton>
              <IconButton disabled={current_page_hm === (total_pages_hm - 1) || isGetHotelManager} onClick={onNext}>
                <KeyboardArrowRight />
              </IconButton>
            </Grid>
            : <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
              <h4 style={{ fontSize: 14 }}>{`Trang ${current_page + 1} / ${total_pages}`}</h4>
              <IconButton disabled={current_page === 0 || isLoading ? true : false}>
                <KeyboardArrowLeft onClick={onPrevious} />
              </IconButton>
              <IconButton disabled={current_page === (total_pages - 1) || isLoading ? true : false} onClick={onNext}>
                <KeyboardArrowRight />
              </IconButton>
            </Grid>}
      </div>
    );
  }
}

MyCustomTable.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    total_pages: state.listHotelReducer.total_pages,
    listHotel: state.listHotelReducer.listHotel,
    isLoading: state.listHotelReducer.isLoading,
    current_page: state.listHotelReducer.current_page,

    total_pages_hm: state.userDetailReducer.total_pages_hm,
    hotelManager: state.userDetailReducer.hotelManager,
    isGetHotelManager: state.userDetailReducer.isGetHotelManager,
    current_page_hm: state.userDetailReducer.current_page_hm
  }
}

const redux = connect(mapStateToProps)(MyCustomTable)

export default withStyles(tableStyle)(redux);
