import React, { Component } from "react";
import {
  Slide,
  DialogActions,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  Button
} from "@material-ui/core";

function Transition(props) {
  return <Slide direction="up" {...props} />;
}

export default class MyDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      content: "",
      isLogout: false,
      isDelete: false
    };
  }

  handleClickOpen = (title, content, isLogout, isDelete) => {
    this.setState({
      open: true,
      title,
      content,
      isLogout: isLogout ? isLogout : false,
      isDelete: isDelete
    });
  };

  handleClose = () => {
    if (this.state.isLogout) {
      this.setState({ open: false });
      this.props.logout();
    } else if (this.state.isDelete) {
      this.setState({ open: false });
      this.props.delete()
    } else {
      this.setState({ open: false });
    }
  };

  handleCloseNormal = () => {
    this.setState({ open: false, isLogout: false, title: "", content: "" });
  };

  render() {
    const { title, content, isLogout, isDelete } = this.state;
    return (
      <div>
        <Dialog
          open={this.state.open}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.handleCloseNormal}
          // disableBackdropClick={true}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle id="alert-dialog-slide-title">{title}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {content}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Đồng ý
            </Button>
            {isLogout || isDelete && <Button onClick={this.handleCloseNormal} color="primary">
              Huỷ
            </Button>}
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
