import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import { connect } from 'react-redux';
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { Grid, IconButton } from "@material-ui/core";
class TableHotel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
    };
  }

  render() {
    const {
      classes,
      tableHead,
      tableHeaderColor,
      onSelectTable,
      isManager,
      current_page,
      total_pages,
      listHotel,
      onPrevious,
      onNext,
      isLoading,
      hotelManager,
      total_pages_hm,
      isGetHotelManager,
      current_page_hm
    } = this.props;
    if (isManager) {
      if (hotelManager && hotelManager.length !== 0) {
        return (
          <div className={classes.tableResponsive}>
            <Table className={classes.table}>
              {tableHead !== undefined ? (
                <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
                  <TableRow>
                    {tableHead.map((prop, key) => {
                      return (
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                          key={key}
                        >
                          {prop}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                </TableHead>
              ) : null}
              <TableBody>
                {isManager
                  ? hotelManager
                    .map((prop, key) => {
                      return (
                        <TableRow key={key} onClick={() => onSelectTable(prop)}>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.name}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.address}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.ratings}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {(Math.floor(prop.hotel.point * 10) / 10) + "/5"}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.description}
                          </TableCell>
                        </TableRow>
                      );
                    })
                  : listHotel
                    .map((prop, key) => {
                      return (
                        <TableRow key={key} onClick={() => onSelectTable(prop)}>
                          <TableCell className={classes.tableCell}>
                            {prop.name}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.address}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.ratings}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {(Math.floor(prop.point * 10) / 10) + "/5"}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.description}
                          </TableCell>
                        </TableRow>
                      );
                    })}
              </TableBody>
            </Table>
            {isManager
              ? <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
                <h4 style={{ fontSize: 14 }}>{`Trang ${current_page_hm + 1} / ${total_pages_hm}`}</h4>
                <IconButton disabled={current_page_hm === 0 || isGetHotelManager}>
                  <KeyboardArrowLeft onClick={onPrevious} />
                </IconButton>
                <IconButton disabled={current_page_hm === (total_pages_hm - 1) || isGetHotelManager} onClick={onNext}>
                  <KeyboardArrowRight />
                </IconButton>
              </Grid>
              : <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
                <h4 style={{ fontSize: 14 }}>{`Trang ${current_page + 1} / ${total_pages}`}</h4>
                <IconButton disabled={current_page === 0 || isLoading ? true : false}>
                  <KeyboardArrowLeft onClick={onPrevious} />
                </IconButton>
                <IconButton disabled={current_page === (total_pages - 1) || isLoading ? true : false} onClick={onNext}>
                  <KeyboardArrowRight />
                </IconButton>
              </Grid>}
          </div>
        );
      } else {
        return (
          <div className={classes.tableResponsive}>
            <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
              <h1 style={{ fontSize: 18 }}>Không có dữ liệu</h1>
            </Grid>
          </div>
        )
      }
    } else {
      if (listHotel && listHotel.length !== 0) {
        return (
          <div className={classes.tableResponsive}>
            <Table className={classes.table}>
              {tableHead !== undefined ? (
                <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
                  <TableRow>
                    {tableHead.map((prop, key) => {
                      return (
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                          key={key}
                        >
                          {prop}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                </TableHead>
              ) : null}
              <TableBody>
                {isManager
                  ? hotelManager
                    .map((prop, key) => {
                      return (
                        <TableRow key={key} onClick={() => onSelectTable(prop)}>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.name}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.address}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.ratings}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {(Math.floor(prop.hotel.point * 10) / 10) + "/5"}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.hotel.description}
                          </TableCell>
                        </TableRow>
                      );
                    })
                  : listHotel
                    .map((prop, key) => {
                      return (
                        <TableRow key={key} onClick={() => onSelectTable(prop)}>
                          <TableCell className={classes.tableCell}>
                            {prop.name}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.address}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.ratings}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {(Math.floor(prop.point * 10) / 10) + "/5"}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {prop.description}
                          </TableCell>
                        </TableRow>
                      );
                    })}
              </TableBody>
            </Table>
            {isManager
              ? <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
                <h4 style={{ fontSize: 14 }}>{`Trang ${current_page_hm + 1} / ${total_pages_hm}`}</h4>
                <IconButton disabled={current_page_hm === 0 || isGetHotelManager}>
                  <KeyboardArrowLeft onClick={onPrevious} />
                </IconButton>
                <IconButton disabled={current_page_hm === (total_pages_hm - 1) || isGetHotelManager} onClick={onNext}>
                  <KeyboardArrowRight />
                </IconButton>
              </Grid>
              : <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
                <h4 style={{ fontSize: 14 }}>{`Trang ${current_page + 1} / ${total_pages}`}</h4>
                <IconButton disabled={current_page === 0 || isLoading ? true : false}>
                  <KeyboardArrowLeft onClick={onPrevious} />
                </IconButton>
                <IconButton disabled={current_page === (total_pages - 1) || isLoading ? true : false} onClick={onNext}>
                  <KeyboardArrowRight />
                </IconButton>
              </Grid>}
          </div>
        );
      } else {
        return (
          <div className={classes.tableResponsive}>
            <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
              <h1 style={{ fontSize: 18 }}>Không có dữ liệu</h1>
            </Grid>
          </div>
        )
      }
    }
  }
}



TableHotel.defaultProps = {
  tableHeaderColor: "gray"
};

TableHotel.propTypes = {
  classes: PropTypes.object.isRequired,
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string)
};

const mapStateToProps = state => {
  return {
    total_pages: state.listHotelReducer.total_pages,
    listHotel: state.listHotelReducer.listHotel,
    isLoading: state.listHotelReducer.isLoading,
    current_page: state.listHotelReducer.current_page,

    total_pages_hm: state.userDetailReducer.total_pages_hm,
    hotelManager: state.userDetailReducer.hotelManager,
    isGetHotelManager: state.userDetailReducer.isGetHotelManager,
    current_page_hm: state.userDetailReducer.current_page_hm
  }
}

const redux = connect(mapStateToProps)(TableHotel)

export default withStyles(tableStyle)(redux);