import React, { Component } from 'react'
import {
    Slide,
    DialogActions,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    Button,
    TextField,
    Grid
} from "@material-ui/core";
import withStyles from "@material-ui/core/styles/withStyles";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import { connect } from 'react-redux';
import MySnackBar from '../../../components/SnackBar';
import { getDetailVerify, changeVerifyManager, removeDataVerify } from '../../../store/actions/verifyManagerAction';
import ItemInfo from './ItemInfo';
import Loading from '../../../components/Loading';
// import Loading from '../../components/Loading';
const USER_NAME_REGEX = /^(?=.{8,100}$)(?![_.])(?!.*[_.]{2})[a-zA-Z0-9._]+(?<![_.])$/
const PHONE_REGEX = /^\(?([0-9]{3})\)?[-.\s]?([0-9]{3})[-.\s]?([0-9]{4})$/

class DialogVerify extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        }

    }
    handleOpen = (id) => {
        const { accessToken } = this.props;
        this.setState({ id })
        this.props.onGetDetail(id, accessToken)
        this.setState({ open: true })
    }

    handleClose = () => {
        this.setState({ open: false })

        this.props.onRemove()
    }

    callApi = (type) => {
        const { id } = this.state
        const { accessToken } = this.props
        const body = {
            status: type
        }

        this.props.onChangeStatus(id, JSON.stringify(body), accessToken)
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };


    componentWillReceiveProps(nextProps) {
        if (nextProps && !!!nextProps.isChangeStatus && !!nextProps.changeSuccess && !!!nextProps.changeError) {
            this.openSnackBar("success", "Xứ lý thành công ! ");
            this.handleClose()
            this.props.close()
        }
        if (nextProps && !!!nextProps.isChangeStatus && !!!nextProps.changeSuccess && nextProps.changeError) {
            this.openSnackBar("error", `Xử lý thất bại ! ${nextProps.changeError}`);
        }

        if (nextProps && !!!nextProps.isGetDetail && !!!nextProps.getDetailSuccess && nextProps.getDetailError) {
            this.openSnackBar("error", `Lấy thông tin thất bại ! ${nextProps.getDetailError}`);
        }
    }

    render() {
        const { isGetDetail, verifyDetail, isChangeStatus } = this.props
        return (
            <div>
                <Dialog
                    open={this.state.open}
                    onClose={this.handleClose}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle id="alert-dialog-slide-title">{'Thông tin tài khoản'}</DialogTitle>
                    {isGetDetail ? <Loading />
                        : <DialogContent style={{ width: '50vh' }}>
                            <ItemInfo name={'Tài khoản'} value={verifyDetail.username} />
                            <ItemInfo name={'Tên'} value={verifyDetail.name} />
                            <ItemInfo name={'Tên đầy đủ'} value={verifyDetail.full_name} />
                            <ItemInfo name={'Email'} value={verifyDetail.email} />
                            <ItemInfo name={'Số điện thoại'} value={verifyDetail.phone} />
                        </DialogContent>}
                    <DialogActions>
                        <Button disabled={isGetDetail || isChangeStatus} onClick={() => this.callApi('verified')} color="primary">
                            Chấp thuận
            </Button>
                        <Button disabled={isGetDetail || isChangeStatus} onClick={() => this.callApi('rejected')} color="primary">
                            Huỷ tài khoản
                        </Button>
                    </DialogActions>
                </Dialog>
                <MySnackBar ref={"MySnackBar"} />
            </div>
        )
    }
}

const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none"
    },
    menu: {
        width: 200
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onGetDetail: (id, accessToken) => {
            dispatch(getDetailVerify(id, accessToken))
        },
        onChangeStatus: (id, body, token) => {
            dispatch(changeVerifyManager(id, body, token))
        },
        onRemove: () => {
            dispatch(removeDataVerify())
        }
    }
}

const mapStateToProps = state => {
    return {
        accessToken: state.profileReducer.accessToken,

        verifyDetail: state.verifyReducer.verifyDetail,
        isGetDetail: state.verifyReducer.isGetDetail,
        getDetailSuccess: state.verifyReducer.getDetailSuccess,
        getDetailError: state.verifyReducer.getDetailError,

        isChangeStatus: state.verifyReducer.isChangeStatus,
        changeSuccess: state.verifyReducer.changeSuccess,
        changeError: state.verifyReducer.changeError,

    }
}

const wrapStyle = withStyles(styles)(DialogVerify)

export default connect(mapStateToProps, mapDispatchToProps)(wrapStyle)

