import { REQUEST_MANAGER_SUCCESS, REQUEST_MANAGER_FAILED, REQUEST_MANAGER, REMOVE_REQUEST, GET_VERIFY_MANAGER_REQUEST, GET_VERIFY_MANAGER_SUCCESS, GET_VERIFY_MANAGER_FAILED, GET_DETAIL_VERIFY_REQUEST, GET_DETAIL_VERIFY_SUCCESS, GET_DETAIL_VERIFY_FAILED, CHANGE_VERIFY_MANAGER_REQUEST, CHANGE_VERIFY_MANAGER_SUCCESS, CHANGE_VERIFY_MANAGER_FAILED, LOG_OUT, REMOVE_DATA_VERIFY } from "../../config/constAct";

const initialState = {
    isRequest: false,
    requestSuccess: false,
    requestError: null,

    listVerify: [],
    isGet: false,
    current_page: 0,
    total_pages: 0,
    getListVerifySuccess: false,
    getListVerifyError: null,

    verifyDetail: {},
    isGetDetail: false,
    getDetailSuccess: false,
    getDetailError: null,

    isChangeStatus: false,
    changeSuccess: false,
    changeError: null
};

const verifyReducer = (state = initialState, action) => {
    switch (action.type) {
        case REQUEST_MANAGER:
            return Object.assign({}, state, {
                isRequest: true,
                requestSuccess: false,
                requestError: null
            })
        case REQUEST_MANAGER_SUCCESS:
            return Object.assign({}, state, {
                isRequest: false,
                requestSuccess: true,
                requestError: null
            })
        case REQUEST_MANAGER_FAILED:
            return Object.assign({}, state, {
                isRequest: false,
                requestSuccess: false,
                requestError: action.payload
            })

        //GET LIST
        case GET_VERIFY_MANAGER_REQUEST:
            return Object.assign({}, state, {
                isGet: true,
                getListVerifySuccess: false,
                getListVerifyError: null,
            })

        case GET_VERIFY_MANAGER_SUCCESS:
            const arr = action.payload
            return Object.assign({}, state, {
                listVerify: arr.content,
                total_pages: arr.total_pages,
                current_page: arr.number,
                isGet: false,
                getListVerifySuccess: false,
                getListVerifyError: null,
            })

        case GET_VERIFY_MANAGER_FAILED:
            return Object.assign({}, state, {
                isGet: false,
                getListVerifySuccess: false,
                getListVerifyError: action.payload,
            })

        //GET DETAIL
        case GET_DETAIL_VERIFY_REQUEST:
            return Object.assign({}, state, {
                verifyDetail: {},
                isGetDetail: true,
                getDetailSuccess: false,
                getDetailError: null
            })

        case GET_DETAIL_VERIFY_SUCCESS:
            return Object.assign({}, state, {
                verifyDetail: action.payload,
                isGetDetail: false,
                getDetailSuccess: true,
                getDetailError: null
            })

        case GET_DETAIL_VERIFY_FAILED:
            return Object.assign({}, state, {
                verifyDetail: {},
                isGetDetail: false,
                getDetailSuccess: false,
                getDetailError: action.payload
            })

        //CHANGE STATUS :
        case CHANGE_VERIFY_MANAGER_REQUEST:
            return Object.assign({}, state, {
                isChangeStatus: true,
                changeSuccess: false,
                changeError: null
            })
        case CHANGE_VERIFY_MANAGER_SUCCESS:
            return Object.assign({}, state, {
                isChangeStatus: false,
                changeSuccess: true,
                changeError: null
            })
        case CHANGE_VERIFY_MANAGER_FAILED:
            return Object.assign({}, state, {
                isChangeStatus: false,
                changeSuccess: false,
                changeError: action.payload
            })

        case REMOVE_DATA_VERIFY:
            return Object.assign({}, state, {
                isRequest: false,
                requestSuccess: false,
                requestError: null,
            
                verifyDetail: {},
                isGetDetail: false,
                getDetailSuccess: false,
                getDetailError: null,

                isChangeStatus: false,
                changeSuccess: false,
                changeError: null
            })
        case LOG_OUT:
            return initialState;
        case REMOVE_REQUEST:
        default:
            return initialState
    }
}

export default verifyReducer;