import React, { Component } from "react";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Loading from "../../../../components/Loading";
import RoomDetail from "./RoomDetail";
import { Grid } from '@material-ui/core'

import { connect } from 'react-redux';
import { getRooms } from "../../../../store/actions/detailHotelAction";

class RoomContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    const { id } = this.props
    this.props.onGetRooms(id);
  }

  render() {
    const { classes, isLoadingRooms, rooms } = this.props;
    return (
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>Các loại phòng </h4>
        </CardHeader>
        <CardBody>
          {isLoadingRooms ? (
            <Loading />
          ) : (
              <Grid>
                {rooms.length !== 0
                  ? rooms.map(item => <RoomDetail item={item} classes={classes} />)
                  :
                  <Grid style={{ display: 'flex', justifyContent: 'center' }}>
                    <h1 style={{ fontSize: 18 }}>Chưa có thông tin về giá phòng</h1>
                  </Grid>
                }
              </Grid>
            )}
        </CardBody>
      </Card>
    );
  }
}


const mapDispatchToProps = dispatch => {
  return {
    onGetRooms: id => {
      dispatch(getRooms(id));
    },
  };
};

const mapStateToProps = state => {
  return {
    rooms: state.detailHotelReducer.rooms,
    isLoadingRooms: state.detailHotelReducer.isLoadingRooms,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoomContainer)