import React, { Component } from 'react'
import {
    DialogActions,
    Dialog,
    DialogTitle,
    DialogContent,
    Button,
    Grid,
} from "@material-ui/core";
import withStyles from "@material-ui/core/styles/withStyles";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import { connect } from 'react-redux';
import { requestManager, removeRequest } from '../../store/actions/verifyManagerAction';
import MySnackBar from '../../components/SnackBar';
import InputField from '../../components/InputField';
import ValidateRegex from '../../utils/ValidateRegex';

class SignUpDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        }

    }
    handleOpen = () => {
        this.setState({ open: true })
    }

    handleClose = () => {
        this.setState({ open: false })
        this.props.onRemove()
    }

    callApi = () => {
        const { username, full_name, email, phone, name, password, errorUsername, errorPhone } = this.state
        if (!errorPhone && !errorUsername && username && full_name && email && phone && name && password) {
            const body = {
                email: email,
                full_name: full_name,
                name: name,
                password: password,
                phone: phone,
                username: username
            }

            this.props.onRequestManager(JSON.stringify(body))
        } else {
            alert('Vui lòng nhập đầy đủ thông tin')
        }
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };


    componentWillReceiveProps(nextProps) {
        if (nextProps && !!!nextProps.isRequest && !!nextProps.requestSuccess && !!!nextProps.requestError) {
            this.openSnackBar("success", "Gửi đăng ký thành công ! Vui lòng đợi phê duyệt ");
            this.handleClose()
        }
        if (nextProps && !!!nextProps.isRequest && !!!nextProps.requestSuccess && nextProps.requestError) {
            this.openSnackBar("error", `Gửi đăng ký thất bại ! ${nextProps.requestError}`);
        }
    }

    render() {
        const { isRequest } = this.props
        const { username, full_name, email, phone, name, password } = this.state
        return (
            <div>
                <Dialog
                    open={this.state.open}
                    onClose={this.handleCloseNormal}
                    disableBackdropClick={true}
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle id="alert-dialog-slide-title">{'Đăng ký'}</DialogTitle>
                    <DialogContent>
                        <Grid >
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        regex={ValidateRegex.username}
                                        id="standard-name"
                                        label="Tài khoản"
                                        value={username}
                                        handleChange={text => this.setState({ username: text })}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        id="standard-name"
                                        label="Mật khẩu"
                                        type="password"
                                        value={password}
                                        handleChange={text => this.setState({ password: text })}
                                    />
                                </GridItem>
                            </GridContainer>
                            <GridContainer>
                            </GridContainer>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        regex={ValidateRegex.name}
                                        id="standard-name"
                                        label="Tên"
                                        value={name}
                                        handleChange={text => this.setState({ name: text })}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        regex={ValidateRegex.full_name}
                                        id="standard-name"
                                        label="Tên đầy đủ"
                                        value={full_name}
                                        handleChange={text => this.setState({ full_name: text })}
                                    />
                                </GridItem>

                            </GridContainer>
                            <GridContainer>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        regex={ValidateRegex.email}
                                        id="standard-name"
                                        label="Email"
                                        value={email}
                                        handleChange={text => this.setState({ email: text })}
                                    />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                    <InputField
                                        regex={ValidateRegex.phone}
                                        id="standard-name"
                                        label="Số điện thoại"
                                        value={phone}
                                        handleChange={text => this.setState({ phone: text })}
                                    />
                                </GridItem>
                            </GridContainer>
                        </Grid>
                    </DialogContent>
                    <DialogActions>
                        <Button disabled={isRequest} onClick={this.callApi} color="primary">
                            Đồng ý
            </Button>
                        <Button disabled={isRequest} onClick={this.handleClose} color="primary">
                            Huỷ
            </Button>
                    </DialogActions>
                </Dialog>

                <MySnackBar ref={"MySnackBar"} />
            </div>
        )
    }
}

const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none"
    },
    menu: {
        width: 200
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onRequestManager: (body) => {
            dispatch(requestManager(body))
        },
        onRemove: () => {
            dispatch(removeRequest())
        }
    }
}

const mapStateToProps = state => {
    return {
        isRequest: state.verifyReducer.isRequest,
        requestSuccess: state.verifyReducer.requestSuccess,
        requestError: state.verifyReducer.requestError,
    }
}

const wrapStyle = withStyles(styles)(SignUpDialog)

export default connect(mapStateToProps, mapDispatchToProps)(wrapStyle)