import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import ListUserManager from "./ListUserManager";
import ListUserClient from "./ListUserClient";
import UserDetail from "./UserDetail";
import CreateUser from "./CreateUser"

export default class UserController extends Component {
  render() {
    return (
      <Switch >
        <Route path={`/main/listuser/manager`} component={ListUserManager} />
        <Route path={`/main/listuser/client`} component={ListUserClient} />
        <Route path={`/main/listuser/detail`} component={UserDetail} {... this.props}/>
        <Route path={`/main/listuser/create`} component={CreateUser} />
      </Switch>
    );
  }
}
