import React, { Component } from "react";
import PropTypes from "prop-types";
import { Switch, Route } from "react-router-dom";

import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
import withStyles from "@material-ui/core/styles/withStyles";
import Navbar from "components/Navbars/Navbar.js";
import Sidebar from '../../components/Sidebar'
import containerStyle from "../../assets/jss/material-dashboard-react/containers/containerStyle.js";

import image from "../../assets/img/sidebar-2.jpg";
import logo from "../../assets/img/reactlogo.png";
import { adminRoute, managerRoute } from "./routes";
import MyDialog from "../../components/Dialog/index.js";
import { logout } from "../../store/actions/userAction";
import { connect } from 'react-redux';
import { MANAGER } from "../../config/constAct";

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      image: image,
      color: "blue",
      hasImage: true,
      fixedClasses: "dropdown show",
      mobileOpen: false
    };
  }
  handleImageClick = image => {
    this.setState({ image: image });
  };

  handleColorClick = color => {
    this.setState({ color: color });
  };

  handleFixedClick = () => {
    if (this.state.fixedClasses === "dropdown") {
      this.setState({ fixedClasses: "dropdown show" });
    } else {
      this.setState({ fixedClasses: "dropdown" });
    }
  };

  handleDrawerToggle = () => {
    this.setState({ mobileOpen: !this.state.mobileOpen });
  };

  getRoute() {
    return this.props.location.pathname !== "/admin/maps";
  }

  resizeFunction = () => {
    if (window.innerWidth >= 960) {
      this.setState({ mobileOpen: false });
    }
  };

  componentDidMount() {
    if (navigator.platform.indexOf("Win") > -1) {
      const ps = new PerfectScrollbar(this.refs.mainPanel);
    }
    window.addEventListener("resize", this.resizeFunction);
  }

  componentDidUpdate(e) {
    if (e.history.location.pathname !== e.location.pathname) {
      this.refs.mainPanel.scrollTop = 0;
      if (this.state.mobileOpen) {
        this.setState({ mobileOpen: false });
      }
    }
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.resizeFunction);
  }

  handleLogout = () => {
    this.refs.MyDialog.handleClickOpen("Đăng xuất", "Bạn có chắc muốn đăng xuất ?", true)
  }

  logout = () => {
    this.props.onLogout()
    this.props.history.push('/')
  }

  switchRoutes = () => {
    const { role } = this.props
    // console.log('adminROute', adminRoute)
    // console.log('role', role)
    return (
      <Switch>
        {role === MANAGER
          ? managerRoute.map((prop, key) => {
            if (prop.path) {
              return (
                <Route
                  path={prop.layout + prop.path}
                  component={prop.component}
                  key={key}
                />
              );
            }
          })
          : adminRoute.map((prop, key) => {
            if (prop.path) {
              return (
                <Route
                  path={prop.layout + prop.path}
                  component={prop.component}
                  key={key}
                />
              );
            }
          })}
      </Switch>
    );
  }

  render() {
    const { classes, role, ...rest } = this.props;
    return (
      <div className={classes.wrapper}>
        <Sidebar
          routes={role === MANAGER ? managerRoute : adminRoute}
          logoText={"Welcome"}
          logo={logo}
          image={this.state.image}
          handleDrawerToggle={this.handleDrawerToggle}
          open={this.state.mobileOpen}
          color={this.state.color}
          {...rest}
        />
        <div className={classes.mainPanel} ref="mainPanel">
          <Navbar
            routes={role === MANAGER ? managerRoute : adminRoute}
            handleDrawerToggle={this.handleDrawerToggle}
            {...rest}
            handleLogout={this.handleLogout}
          />
          <div className={classes.content}>
            <div className={classes.container}>{this.switchRoutes()}</div>
          </div>
        </div>
        <MyDialog ref={"MyDialog"} logout={this.logout} />
      </div>
    );
  }
}

Main.propTypes = {
  classes: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => {
  return {
    onLogout: () => {
      dispatch(logout())
    }
  }
}

const mapStateToProps = state => {
  return {
    role: state.profileReducer.role
  }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(Main)

export default withStyles(containerStyle)(redux);
