import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { Grid, IconButton } from "@material-ui/core";
import { connect } from 'react-redux';
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";

class TableUser extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
    };
  }

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = event => {
    this.setState({ rowsPerPage: event.target.value });
  };

  render() {
    const { classes,
      onSelectTable,
      tableHead,
      tableHeaderColor,
      isClient,
      listClient,
      listManager,
      isGet,
      current_page_client,
      current_page_manager,
      total_pages_client,
      total_pages_manager,
      onNext,
      onPrevious
    } = this.props;
    return (
      <div className={classes.tableResponsive}>
        <Table className={classes.table}>
          {tableHead !== undefined ? (
            <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
              <TableRow>
                {tableHead.map((prop, key) => {
                  return (
                    <TableCell
                      className={
                        classes.tableCell + " " + classes.tableHeadCell
                      }
                      key={key}
                    >
                      {prop}
                    </TableCell>
                  );
                })}
              </TableRow>
            </TableHead>
          ) : null}
          <TableBody>
            {isClient ? listClient
              .map((prop, key) => {
                return (
                  <TableRow key={key} onClick={() => onSelectTable(prop)}>
                    <TableCell className={classes.tableCell}>
                      {prop.username}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {prop.name}
                    </TableCell>
                  </TableRow>
                );
              })
              : listManager
                .map((prop, key) => {
                  return (
                    <TableRow key={key} onClick={() => onSelectTable(prop)}>
                      <TableCell className={classes.tableCell}>
                        {prop.username}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {prop.name}
                      </TableCell>
                    </TableRow>
                  );
                })}
          </TableBody>
        </Table>
        {isClient ?
            <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
              <h4 style={{ fontSize: 14 }}>{`Trang ${current_page_client + 1} / ${total_pages_client}`}</h4>
              <IconButton disabled={current_page_client === 0 || isGet}>
                <KeyboardArrowLeft onClick={onPrevious} />
              </IconButton>
              <IconButton disabled={current_page_client === total_pages_client - 1 || isGet} onClick={onNext}>
                <KeyboardArrowRight />
              </IconButton>
            </Grid>
            :
            <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
              <h4 style={{ fontSize: 14 }}>{`Trang ${current_page_manager + 1} / ${total_pages_manager}`}</h4>
              <IconButton disabled={current_page_manager === 0 || isGet}>
                <KeyboardArrowLeft onClick={onPrevious} />
              </IconButton>
              <IconButton disabled={current_page_manager === total_pages_manager - 1 || isGet} onClick={onNext}>
                <KeyboardArrowRight />
              </IconButton>
            </Grid>}
      </div>
    );
  }
}

TableUser.defaultProps = {
  tableHeaderColor: "gray"
};

TableUser.propTypes = {
  classes: PropTypes.object.isRequired,
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string),
  tableData: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string))
};

const mapStateToProps = state => {
  return {
    listManager: state.userDetailReducer.listManager,
    current_page_manager: state.userDetailReducer.current_page_manager,
    total_pages_manager: state.userDetailReducer.total_pages_manager,

    listClient: state.userDetailReducer.listClient,
    current_page_client: state.userDetailReducer.current_page_client,
    total_pages_client: state.userDetailReducer.total_pages_client,

    isGet: state.userDetailReducer.isGet,
    getSuccess: state.userDetailReducer.getSuccess,
    errorGet: state.userDetailReducer.errorGet,
  }
}
const redux = connect(mapStateToProps)(TableUser)

export default withStyles(tableStyle)(redux);
