import {
  GET_USER_DETAIL_REQUEST,
  GET_USER_DETAIL_SUCCESS,
  GET_USER_DETAIL_FAILED,
  GET_HOTEL_MANAGER_REQUEST,
  GET_HOTEL_MANAGER_SUCCESS,
  GET_HOTEL_MANAGER_FAILED,
  DELETE_USER_REQUEST,
  DELETE_USER_SUCCESS,
  DELETE_USER_FAILED,
  UPDATE_USER_REQUEST,
  UPDATE_USER_SUCCESS,
  UPDATE_USER_FAILED,
  CREATE_USER_REQUEST,
  CREATE_USER_SUCCESS,
  CREATE_USER_FAILED,
  ADD_HOTEL_MANAGER_REQUEST,
  ADD_HOTEL_MANAGER_SUCCESS,
  ADD_HOTEL_MANAGER_FAILED,
  REMOVE_USER_DETAIL,
  DELETE_HOTEL_MANAGER_REQUEST,
  DELETE_HOTEL_MANAGER_SUCCESS,
  DELETE_HOTEL_MANAGER_FAILED,
  LOG_OUT,
  GET_LIST_USER_MANAGER_REQUEST,
  GET_LIST_USER_MANAGER_SUCCESS,
  GET_LIST_USER_MANAGER_FAILED,
  GET_LIST_USER_CLIENT_REQUEST,
  GET_LIST_USER_CLIENT_SUCCESS,
  GET_LIST_USER_CLIENT_FAILED,
  REMOVE_CACHE,
  UPLOAD_IMAGE_USER_REQUEST,
  UPLOAD_IMAGE_USER_SUCCESS,
  UPLOAD_IMAGE_USER_FAILED
} from "../../config/constAct";

const initialState = {
  userDetail: {},
  isGetUserDetail: false,
  getDetailSuccess: false,
  errorGetDetail: null,

  hotelManager: [],
  current_page_hm: 0,
  total_pages_hm: 0,
  isGetHotelManager: false,
  getHotelManagerSuccess: false,
  errorGetHotelManager: null,
  done: false,

  isAddHotelManager: false,
  addHotelManagerSuccess: false,
  errorAdd: null,

  isDeleting: false,
  deleteSuccess: false,
  errorDelete: null,

  listManager: [],
  current_page_manager: 0,
  total_pages_manager: 0,

  listClient: [],
  current_page_client: 0,
  total_pages_client: 0,

  isGet: false,
  getSuccess: false,
  errorGet: null,

  isUpdate: false,
  updateSuccess: false,
  errorUpdate: null,

  isCreating: false,
  createSuccess: false,
  errorCreate: null,

  idDeleteHotel: false,
  deleteHotelSuccess: false,
  deletehotelError: null,

  isUpload: false,
  uploadSuccess: false,
  uploadError: null
};

const userDetailReducer = (state = initialState, action) => {
  switch (action.type) {
    //GET USER DETAIL
    case GET_USER_DETAIL_REQUEST:
      return Object.assign({}, state, {
        userDetail: {},
        isGetUserDetail: true,
        getDetailSuccess: false,
        errorGetDetail: null
      });

    case GET_USER_DETAIL_SUCCESS:
      return Object.assign({}, state, {
        userDetail: action.payload,
        isGetUserDetail: false,
        getDetailSuccess: true,
        errorGetDetail: null
      });

    case GET_USER_DETAIL_FAILED:
      return Object.assign({}, state, {
        userDetail: {},
        isGetUserDetail: false,
        getDetailSuccess: false,
        errorGetDetail: action.payload
      });

    //GET HOTEL MANAGER
    case GET_HOTEL_MANAGER_REQUEST:
      return Object.assign({}, state, {
        // hotelManager: [],
        isGetHotelManager: true,
        getHotelManagerSuccess: false,
        errorGetHotelManager: null,
        done: false
      });

    case GET_HOTEL_MANAGER_SUCCESS:
      const arr = action.payload;
      return Object.assign({}, state, {
        hotelManager: arr.content,
        total_pages_hm: arr.total_pages,
        current_page_hm: arr.number,
        isGetHotelManager: false,
        getHotelManagerSuccess: false,
        errorGetHotelManager: null,
        done: false
      });

    case GET_HOTEL_MANAGER_FAILED:
      return Object.assign({}, state, {
        // hotelManager: [],
        isGetHotelManager: false,
        getHotelManagerSuccess: false,
        errorGetHotelManager: action.payload,
        done: false
      });

    //DELETE USER
    case DELETE_USER_REQUEST:
      return Object.assign({}, state, {
        isDeleting: true,
        deleteSuccess: false,
        errorDelete: null
      });

    case DELETE_USER_SUCCESS:
      return Object.assign({}, state, {
        isDeleting: false,
        deleteSuccess: true,
        errorDelete: null
        // listManager: state.listManager.filter(item => item.id !== action.payload),
        // listClient: state.listClient.filter(item => item.id !== action.payload)
      });
    case DELETE_USER_FAILED:
      return Object.assign({}, state, {
        isDeleting: false,
        deleteSuccess: false,
        errorDelete: action.payload
      });

    //GET_LIST_USER MANAGER
    case GET_LIST_USER_MANAGER_REQUEST:
      return Object.assign({}, state, {
        isGet: true,
        getSuccess: false,
        errorGet: null
      });

    case GET_LIST_USER_MANAGER_SUCCESS:
      const arrManager = action.payload;
      return Object.assign({}, state, {
        listManager: arrManager.content,
        total_pages_manager: arrManager.total_pages,
        current_page_manager: arrManager.number,
        isGet: false,
        getSuccess: true,
        errorGet: null
      });

    case GET_LIST_USER_MANAGER_FAILED:
      return Object.assign({}, state, {
        isGet: false,
        getSuccess: false,
        errorGet: action.payload
      });

    //GET LIST USER CLIENT
    case GET_LIST_USER_CLIENT_REQUEST:
      return Object.assign({}, state, {
        listManager: [],
        listClient: [],
        isGet: true,
        getSuccess: false,
        errorGet: null
      });

    case GET_LIST_USER_CLIENT_SUCCESS:
      const arrClient = action.payload;
      return Object.assign({}, state, {
        listClient: arrClient.content,
        total_pages_client: arrClient.total_pages,
        current_page_client: arrClient.number,
        isGet: false,
        getSuccess: true,
        errorGet: null
      });

    case GET_LIST_USER_CLIENT_FAILED:
      return Object.assign({}, state, {
        isGet: false,
        getSuccess: false,
        errorGet: action.payload
      });

    //UPDATE_USER
    case UPDATE_USER_REQUEST:
      return Object.assign({}, state, {
        isUpdate: true,
        updateSuccess: false,
        errorUpdate: null
      });
    case UPDATE_USER_SUCCESS:
      return Object.assign({}, state, {
        isUpdate: false,
        updateSuccess: true,
        errorUpdate: null,
        userDetail: action.payload
      });
    case UPDATE_USER_FAILED:
      return Object.assign({}, state, {
        isUpdate: false,
        updateSuccess: false,
        errorUpdate: action.payload
      });

    //CREATE USER
    case CREATE_USER_REQUEST:
      return Object.assign({}, state, {
        isCreating: true,
        createSuccess: false,
        errorCreate: null
      });
    case CREATE_USER_SUCCESS:
      return Object.assign({}, state, {
        isCreating: false,
        createSuccess: true,
        errorCreate: null
      });
    case CREATE_USER_FAILED:
      return Object.assign({}, state, {
        isCreating: false,
        createSuccess: false,
        errorCreate: action.payload
      });

    //ADD HOTEL MANAGER
    case ADD_HOTEL_MANAGER_REQUEST:
      return Object.assign({}, state, {
        isAddHotelManager: true,
        addHotelManagerSuccess: false,
        errorAdd: null
      });
    case ADD_HOTEL_MANAGER_SUCCESS:
      return Object.assign({}, state, {
        isAddHotelManager: false,
        addHotelManagerSuccess: true,
        errorAdd: null,
        done: true
      });
    case ADD_HOTEL_MANAGER_FAILED:
      return Object.assign({}, state, {
        isAddHotelManager: false,
        addHotelManagerSuccess: false,
        errorAdd: action.payload
      });

    //DELETE HOTEL MANAGER
    case DELETE_HOTEL_MANAGER_REQUEST:
      return Object.assign({}, state, {
        idDeleteHotel: true,
        deleteHotelSuccess: false,
        deletehotelError: null
      });
    case DELETE_HOTEL_MANAGER_SUCCESS:
      return Object.assign({}, state, {
        idDeleteHotel: false,
        deleteHotelSuccess: true,
        deletehotelError: null,
        done: true
      });

    case DELETE_HOTEL_MANAGER_FAILED:
      return Object.assign({}, state, {
        idDeleteHotel: false,
        deleteHotelSuccess: false,
        deletehotelError: action.payload
      });

    //UPLOAD IMAGE
    case UPLOAD_IMAGE_USER_REQUEST:
      return Object.assign({}, state, {
        isUpload: true,
        uploadSuccess: false,
        uploadError: null
      });

    case UPLOAD_IMAGE_USER_SUCCESS:
      return Object.assign({}, state, {
        isUpload: false,
        uploadSuccess: true,
        uploadError: null,
        userDetail: Object.assign({}, state.userDetail, {
          picture: action.payload
        })
      });

    case UPLOAD_IMAGE_USER_FAILED:
      return Object.assign({}, state, {
        isUpload: false,
        uploadSuccess: false,
        uploadError: action.payload
      });

    case REMOVE_CACHE:
      return Object.assign({}, state, {
        done: false,

        isAddHotelManager: false,
        addHotelManagerSuccess: false,
        errorAdd: null,

        idDeleteHotel: false,
        deleteHotelSuccess: false,
        deletehotelError: null
      });

    case REMOVE_USER_DETAIL:
      return Object.assign({}, state, {
        getDetailSuccess: false,
        errorGetDetail: null,

        getHotelManagerSuccess: false,
        errorGetHotelManager: null,
        done: false,

        addHotelManagerSuccess: false,
        errorAdd: null,

        deleteSuccess: false,
        errorDelete: null,

        getSuccess: false,
        errorGet: null,

        updateSuccess: false,
        errorUpdate: null,

        createSuccess: false,
        errorCreate: null,

        deleteHotelSuccess: false,
        deletehotelError: null,

        uploadSuccess: false,
        uploadError: null
      });

    case LOG_OUT:
      return initialState;

    default:
      return state;
  }
};

export default userDetailReducer;
