import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import numeral from 'numeral'
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import {connect} from 'react-redux';
import { Button } from "@material-ui/core";
import moment from "moment";
import 'moment/locale/vi'

class TableReview extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
    };
  }

  render() {
    const { classes, tableHead, tableData, tableHeaderColor, onSelectTable, isDataHotel } = this.props;
    return (
      <div className={classes.tableResponsive}>
        <Table className={classes.table}>
          {tableHead !== undefined ? (
            <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
              <TableRow>
                {tableHead.map((prop, key) => {
                  return (
                    <TableCell
                      className={classes.tableCell + " " + classes.tableHeadCell}
                      key={key}
                    >
                      {prop}
                    </TableCell>
                  );
                })}
              </TableRow>
            </TableHead>
          ) : null}
          <TableBody>
            {tableData.map((prop, key) => {
              return (
                <TableRow key={key}>
                  <TableCell className={classes.tableCell}>
                    {prop.user.name}
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    {prop.rating_point}
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    {moment(prop.created_time).format('LLL')}
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    {prop.comment}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </div>
    );
  }
}

TableReview.defaultProps = {
  tableHeaderColor: "gray"
};

TableReview.propTypes = {
  classes: PropTypes.object.isRequired,
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string),
};

const mapStateToProps = state => {
  return {
    isLoadingReview: state.detailHotelReducer.isLoadingReview,
    review: state.detailHotelReducer.review,
    getReviewSuccess: state.detailHotelReducer.getReviewSuccess,
    errorGetReview: state.detailHotelReducer.errorGetReview,
  }
}

const redux = connect(mapStateToProps)(TableReview)

export default withStyles(tableStyle)(redux);
