// GET DETAIL HOTEL
export const getDetailHotelRequest = () => ({
  type: GET_DETAIL_HOTEL_REQUEST
});

export const getDetailHotelSuccess = data => ({
  type: GET_DETAIL_HOTEL_SUCCESS,
  payload: data
});

export const getDetailHotelFailed = error => ({
  type: GET_DETAIL_HOTEL_FAILED,
  payload: error
});

export const getDetailHotel = id => {
  return async dispatch => {
    dispatch(getDetailHotelRequest());
    try {
      let config = {
        method: "GET",
        headers: {
          Accept: "application/json"
        }
      };

      let response = await fetch(`${HOST}/hotels/${id}`, config);
      let json = await response.json();
      console.log('detail hotel', json)
      if (json && json.id) {
        dispatch(getDetailHotelSuccess(json));
      } else {
        dispatch(getDetailHotelFailed(json.message))
      }
    } catch (error) {
      console.log("error", error);
      dispatch(getDetailHotelFailed(error))
    }
  };
};

//CREATE HOTEL 
export const createHotelRequest = () => ({
  type: CREATE_HOTEL_REQUEST
})

export const createHotelSuccess = (data) => ({
  type: CREATE_HOTEL_SUCCESS,
  payload: data
})

export const createHotelFailed = error => ({
  type: CREATE_HOTEL_FAILED,
  payload: error
})

export const createHotel = (body, token) => {
  return async dispatch => {
    dispatch(createHotelRequest())
    try {
      let config = {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token
        },
        body: body
      };
      let response = await fetch(`${HOST}/hotels`, config)
      let json = await response.json();
      console.log('create hotel', json)
      if (json.status && json.status === true) {
        dispatch(createHotelSuccess(json.hotel))
      } else {
        dispatch(createHotelFailed(json.message))
      }
    } catch (error) {
      console.log(error)
      dispatch(createHotelFailed(error))
    }
  }
}

