////UPDATE HOTEL
export const updateHotelRequest = () => ({
  type: UPDATE_HOTEL_REQUEST
})

export const updateHotelSuccess = data => ({
  type: UPDATE_HOTEL_SUCCESS,
  payload: data
});

export const updateHotelFailed = error => ({
  type: UPDATE_HOTEL_FAILED,
  payload: error
});

export const updateHotel = (id, body, token) => {
  return async dispatch => {
    dispatch(updateHotelRequest())
    try {
      let config = {
        method: "PUT",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + token
        },
        body: body
      };

      let response = await fetch(`${HOST}/hotels/${id}`, config)
      let json = await response.json();
      console.log('update hotel', json)

      if (json.status && json.status === true) {
        dispatch(updateHotelSuccess(json.hotel))
      } else {
        dispatch(updateHotelFailed(json.message))
      }
    } catch (error) {
      console.log(error)
      dispatch(updateHotelFailed(error))
    }
  }
}

//CHANGE_STATUS
