//GET REVIEW
export const getReviewRequest = () => ({
  type: GET_REVIEW_REQUEST
});

export const getReviewSuccess = data => ({
  type: GET_REVIEW_SUCCESS,
  payload: data
});

export const getReviewFailed = error => ({
  type: GET_REVIEW_FAILED,
  payload: error
});

export const getReview = (id, number) => {
  return async dispatch => {
    dispatch(getReviewRequest());
    try {
      let config = {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        }
      };

      let response = await fetch(`${HOST}/hotels/${id}/reviews?number=${number}`, config);
      let json = await response.json();
      console.log("get review", json);
      if (json.reviews && typeof (json.reviews.length) !== 'undefined') {
        dispatch(getReviewSuccess(json.reviews));
      } else {
        dispatch(getReviewFailed(json.error));
      }
    } catch (error) {
      console.log("error", error);
      dispatch(getReviewFailed(error));
    }
  };
};


