import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";
import {
    Grid,
    IconButton,
    Paper,
    InputBase
} from "@material-ui/core";
import CustomTable from "../../../components/CustomTable";
import FilterListIcon from "@material-ui/icons/FilterList";
import SearchIcon from "@material-ui/icons/Search";
import Close from "@material-ui/icons/Close"
import { connect } from "react-redux";
import MySnackBar from "../../../components/SnackBar";
import Loading from "../../../components/Loading";
import TableHotel from "../../../components/Table/TableHotel";
import MyDialog from '../../../components/Dialog'
import { getVerifyManager } from "../../../store/actions/verifyManagerAction";
import TableVerify from "./TableVerify";
import DialogVerify from "./DialogVerify";

class VerifyManager extends React.Component {
    componentDidMount() {
        const { accessToken } = this.props
        this.props.onGetVerify(0, accessToken)
    }

    onSelectTable = detail => {
        this.dialogVerify.handleOpen(detail.id)
    }

    onPrevious = () => {
        const { accessToken, current_page } = this.props
        this.props.onGetVerify(current_page - 1, accessToken);
    }

    onNext = () => {
        const { accessToken, current_page } = this.props
        this.props.onGetVerify(current_page + 1, accessToken);
    }

    openSnackBar = (type, message) => {
        const obj = {
            variant: type,
            message: message
        };
        this.refs.MySnackBar.handleOption(obj);
        this.refs.MySnackBar.handleOpen();
    };

    componentWillReceiveProps(nextProps) {
        if (nextProps && !!!nextProps.isGet && !!!nextProps.getListVerifySuccess && nextProps.getListVerifyError) {
            this.openSnackBar("error", `Lấy danh sách thất bại ! ${nextProps.getListVerifyError}`);
        }
    }

    render() {
        const { classes, isGet, current_page, accessToken } = this.props
        return (
            <GridContainer>
                <GridItem xs={12} sm={12} md={12}>
                    <Card>
                        <CardHeader color="primary">
                            <h4 className={classes.cardTitleWhite}>
                                Danh sách tài khoản đợi xét duyệt
              </h4>
                        </CardHeader>
                        <CardBody>
                            {isGet ? <Loading />
                                : <TableVerify
                                    onSelectTable={this.onSelectTable}
                                    tableHeaderColor="primary"
                                    tableHead={[
                                        "Tên ",
                                        "Email",
                                        "Số điện thoại",
                                        "Ngày đăng kí",
                                    ]}
                                    onPrevious={this.onPrevious}
                                    onNext={this.onNext}
                                />}
                        </CardBody>
                        <CardFooter
                            style={{ justifyContent: "flex-end", alignItems: "flex-end" }}
                        >
                        </CardFooter>
                    </Card>
                </GridItem>
                <MySnackBar ref={"MySnackBar"} />
                {/* <MyDialog ref={"MyDialog"} delete={this.delete} /> */}
                <DialogVerify innerRef={ref => this.dialogVerify = ref} close={() => this.props.onGetVerify(current_page, accessToken)}/>
            </GridContainer>
        )
    }
}

const styles = {
    cardCategoryWhite: {
        "&,& a,& a:hover,& a:focus": {
            color: "rgba(255,255,255,.62)",
            margin: "0",
            fontSize: "14px",
            marginTop: "0",
            marginBottom: "0"
        },
        "& a,& a:hover,& a:focus": {
            color: "#FFFFFF"
        }
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
        "& small": {
            color: "#777",
            fontSize: "65%",
            fontWeight: "400",
            lineHeight: "1"
        }
    },
    cardTitle: {
        color: "#00000",
        marginTop: "0px",
        marginLeft: "10px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
        fontSize: 18
    },
    root: {
        padding: "2px 4px",
        display: "flex",
        alignItems: "center",
        width: '100%'
    },
    input: {
        marginLeft: 8,
        flex: 1
    },
    iconButton: {
        padding: 10
    },
    divider: {
        width: 1,
        height: 28,
        margin: 4
    }
};


const mapDispatchToProps = dispatch => {
    return {
        onGetVerify: (page, token) => {
            dispatch(getVerifyManager(page, token))
        }
    }
}

const mapStateToProps = state => {
    return {
        accessToken: state.profileReducer.accessToken,
        isGet: state.verifyReducer.isGet,
        current_page: state.verifyReducer.current_page,
        total_pages: state.verifyReducer.total_pages,
        getListVerifySuccess: state.verifyReducer.getListVerifySuccess,
        getListVerifyError: state.verifyReducer.getListVerifyError
    }
}

const redux = connect(mapStateToProps, mapDispatchToProps)(VerifyManager)

export default withStyles(styles)(redux)