import React from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import { connect } from 'react-redux';
import withStyles from "@material-ui/core/styles/withStyles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
// core components
import tableStyle from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { Grid, IconButton } from "@material-ui/core";
import moment from "moment";
class TableVerify extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            page: 0,
        };
    }

    render() {
        const {
            classes,
            tableHead,
            tableHeaderColor,
            onSelectTable,
            current_page,
            total_pages,
            onPrevious,
            onNext,
            isGet,
            listVerify
        } = this.props;
        console.log('listVerify',listVerify)
        if (listVerify && listVerify.length !== 0) {
            return (
                <div className={classes.tableResponsive}>
                    <Table className={classes.table}>
                        {tableHead !== undefined ? (
                            <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
                                <TableRow>
                                    {tableHead.map((prop, key) => {
                                        return (
                                            <TableCell
                                                className={
                                                    classes.tableCell + " " + classes.tableHeadCell
                                                }
                                                key={key}
                                            >
                                                {prop}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            </TableHead>
                        ) : null}
                        <TableBody>
                            {listVerify
                                .map((prop, key) => {
                                    return (
                                        <TableRow key={key} onClick={() => onSelectTable(prop)}>
                                            <TableCell className={classes.tableCell}>
                                                {prop.full_name}
                                            </TableCell>
                                            <TableCell className={classes.tableCell}>
                                                {prop.email}
                                            </TableCell>
                                            <TableCell className={classes.tableCell}>
                                                {prop.phone}
                                            </TableCell>
                                            <TableCell className={classes.tableCell}>
                                                {moment(prop.created_time).format('LLL')}
                                            </TableCell>
                                            {/* <TableCell className={classes.tableCell}>
                                                {prop.hotel.description}
                                            </TableCell> */}
                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                    </Table>
                    <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end' }}>
                        <h4 style={{ fontSize: 14 }}>{`Trang ${current_page + 1} / ${total_pages}`}</h4>
                        <IconButton disabled={current_page === 0 || isGet}>
                            <KeyboardArrowLeft onClick={onPrevious} />
                        </IconButton>
                        <IconButton disabled={current_page === (total_pages - 1) || isGet} onClick={onNext}>
                            <KeyboardArrowRight />
                        </IconButton>
                    </Grid>
                </div>
            );
        } else {
            return (
                <div className={classes.tableResponsive}>
                    <Grid style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                        <h1 style={{ fontSize: 18 }}>Không có yêu cầu xét duyệt nào</h1>
                    </Grid>
                </div>
            );
        }

    }
}

TableVerify.defaultProps = {
    tableHeaderColor: "gray"
};

TableVerify.propTypes = {
    classes: PropTypes.object.isRequired,
    tableHeaderColor: PropTypes.oneOf([
        "warning",
        "primary",
        "danger",
        "success",
        "info",
        "rose",
        "gray"
    ]),
    tableHead: PropTypes.arrayOf(PropTypes.string)
};

const mapStateToProps = state => {
    return {
        listVerify: state.verifyReducer.listVerify,
        isGet: state.verifyReducer.isGet,
        current_page: state.verifyReducer.current_page,
        total_pages: state.verifyReducer.total_pages,
        getListVerifySuccess: state.verifyReducer.getListVerifySuccess,
        getListVerifyError: state.verifyReducer.getListVerifyError
    }
}

const redux = connect(mapStateToProps)(TableVerify)

export default withStyles(tableStyle)(redux);