import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILED,
  SIGNUP_REQUEST,
  SIGNUP_SUCCESS,
  SIGNUP_FAILED,
  ANOTHER_HOST,
  LOG_OUT,
  CLIENT,
  HOST,
  UPLOAD_AVATAR_REQUEST,
  UPLOAD_AVATAR_SUCCESS,
  UPLOAD_AVATAR_FAILED,
  UPDATE_USER_REQUEST,
  UPDATE_USER_SUCCESS,
  UPDATE_USER_FAILED,
  UPDATE_PROFILE_REQUEST,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_FAILED
} from "../../config/constAct";
//LOGIN WITH ACCOUNT
export const loginRequest = () => ({
  type: LOGIN_REQUEST
});

export const loginSuccess = data => ({
  type: LOGIN_SUCCESS,
  payload: data
});

export const loginFailed = error => ({
  type: LOGIN_FAILED,
  payload: error
});

export const login = (params) => {
  return async dispatch => {
    dispatch(loginRequest());
    try {
      let config = {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: params
      };
      let response = await fetch(`${ANOTHER_HOST}/login`, config);
      let json = await response.json();
      console.log("login", json);
      if (json.status) {
        if (json.user.role === CLIENT) {
          dispatch(loginFailed('Username/Password is wrong'));
        } else {
          dispatch(loginSuccess(json));
        }
      } else {
        dispatch(loginFailed(json.message));
      }
    } catch (error) {
      console.log("error", error);
      dispatch(loginFailed(error));
    }
  };
};

//SIGNUP
export const signUpRequest = () => ({
  type: SIGNUP_REQUEST
});

export const signUpSuccess = data => ({
  type: SIGNUP_SUCCESS,
  payload: data
});

export const signUpFailed = error => ({
  type: SIGNUP_FAILED,
  payload: error
});

export const signup = (params) => {
  return async dispatch => {
    dispatch(signUpRequest());
    try {
      let config = {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: params
      };

      let response = await fetch(`${ANOTHER_HOST}/signup`, config);
      let json = await response.json();
      console.log("signup", json);
      if (json.status) {
        dispatch(signUpSuccess(json.user));
      }
      else {
        dispatch(signUpFailed(json.message))
      }
      dispatch(signUpFailed());
    } catch (error) {
      console.log("error", error);
      dispatch(signUpFailed(error));
    }
  };
};


export const uploadImageRequest = () => ({
  type: UPLOAD_AVATAR_REQUEST
})

export const uploadImageSuccess = (data) => ({
  type: UPLOAD_AVATAR_SUCCESS,
  payload: data
})

export const uploadImageFailed = error => ({
  type: UPLOAD_AVATAR_FAILED,
  payload: error
})

export const uploadImage = (id, body, token) => {
  return async dispatch => {
      dispatch(uploadImageRequest())
      try {
          let config = {
              method: "POST",
              headers: {
                  // Accept: "application/json",
                  Authorization: "Bearer " + token
              },
              body: body
          };

          let response = await fetch(`${HOST}/users/${id}/photos`, config);
          let json = await response.json();
          console.log("upload image", json);
          if (json.status && json.status === true) {
              dispatch(uploadImageSuccess(json.url))
          } else {
              dispatch(uploadImageFailed(json.message))
          }

      } catch (error) {
          console.log("error", error);
          dispatch(uploadImageFailed(error))
      }
  }
}


//UPDATE USER
export const updateUserRequest = () => ({
  type: UPDATE_PROFILE_REQUEST
})

export const updateUserSuccess = data => ({
  type: UPDATE_PROFILE_SUCCESS,
  payload: data
});

export const updateUserFailed = error => ({
  type: UPDATE_PROFILE_FAILED,
  payload: error
});

export const updateUser = (id, body, token) => {
  return async dispatch => {
      dispatch(updateUserRequest())
      try {
          let config = {
              method: "PUT",
              headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json",
                  Authorization: "Bearer " + token
              },
              body: body
          };

          let response = await fetch(`${HOST}/users/${id}`, config)
          let json = await response.json();
          console.log('update user', json)
          if (json.status && json.status === true) {
              dispatch(updateUserSuccess(json.user))
          } else {
              dispatch(updateUserFailed(json.message))
          }
      } catch (error) {
          console.log(error)
          dispatch(updateUserFailed(error))
      }
  }
}


//LOGOUT
export const logoutRequest = () => ({
  type: LOG_OUT
})

export const logout = () => {
  return async dispatch => {
    dispatch(logoutRequest())
  }
}