import React, { Component } from "react";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Loading from "../../../../components/Loading";
import {
  TextField,
  CardMedia,
  Grid,
  Checkbox,
  GridList,
} from "@material-ui/core";
import {
  getDetailHotel,
  updateHotel,
  removeData,
  changeStatus,
  uploadImage,
  removeCache,
  deleteAllImage
} from "../../../../store/actions/detailHotelAction";
import { connect } from "react-redux";
import withStyles from "@material-ui/core/styles/withStyles";
import MySnackBar from "../../../../components/SnackBar";
import { ANOTHER_HOST } from "../../../../config/constAct";
import MyDialog from "../../../../components/Dialog";

const check = require("../../../../assets/img/check.png");
// const check = require('../../../../../public/img/check.png')

const unCheck = require("../../../../assets/img/uncheck.png");
// const unCheck = require("../../../../../public/img/uncheck.png");

class InfoDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      rating: "",
      address: "",
      description: "",
      isUpdate: false,
      hotel: {},
      wifi: false,
      laundry: false,
      bar: false,
      active: false,
      status: false,
      show: false,
      logo: null,
      photo: null
    };
  }

  componentWillMount() {
    const { id } = this.props;
    this.props.onGetDetail(id);
  }

  componentWillReceiveProps(nextProps) {
    if (
      nextProps &&
      !!!nextProps.isChangeStatus &&
      !!nextProps.changeStatusSuccess &&
      !!!nextProps.errorChangeStatus
    ) {
      this.openSnackBar("success", "Chuyển trạng thái thành công");
      this.props.onRemoveCache();
    }
    if (
      nextProps &&
      !!!nextProps.isChangeStatus &&
      !!!nextProps.changeStatusSuccess &&
      nextProps.errorChangeStatus
    ) {
      this.openSnackBar(
        "error",
        `Chuyển trạng thái thất bại ! ${nextProps.errorChangeStatus}`
      );
      this.props.onRemoveCache();
    }

    if (
      nextProps &&
      !!!nextProps.isUpdating &&
      !!nextProps.updateSuccess &&
      !!!nextProps.errorUpdate
    ) {
      this.openSnackBar("success", "Cập nhật thành công");
      this.setState({ isUpdate: !this.state.isUpdate });
      this.props.onRemoveCache();
    }
    if (
      nextProps &&
      !!!nextProps.isUpdating &&
      !!!nextProps.updateSuccess &&
      nextProps.errorUpdate
    ) {
      this.openSnackBar(
        "error",
        `Cập nhật thất bại ! ${nextProps.errorUpdate}`
      );
      this.props.onRemoveCache();
    }

    if (
      nextProps &&
      !!!nextProps.isLoadingHotel &&
      !!!nextProps.getHotelSuccess &&
      nextProps.errorGetHotel
    ) {
      this.openSnackBar(
        "error",
        `Lấy thông tin khách sạn thật bại ! ${nextProps.errorGetHotel}`
      );
      this.props.onRemoveCache();
    }

    if (
      nextProps &&
      !!!nextProps.isUpload &&
      !!nextProps.uploadSuccess &&
      !!!nextProps.uploadError
    ) {
      this.openSnackBar("success", "Cập nhật ảnh thành công");
      this.setState({ show: !this.state.show });
      this.props.onRemoveCache();
    }
    if (
      nextProps &&
      !!!nextProps.isUpload &&
      !!!nextProps.uploadSuccess &&
      nextProps.uploadError
    ) {
      this.openSnackBar(
        "error",
        `Cập nhật ảnh thất bại ! ${nextProps.uploadError}`
      );
      this.props.onRemoveCache();
    }

    if (
      nextProps &&
      !!!nextProps.isDeleteAll &&
      !!nextProps.deleteAllSuccess &&
      !!!nextProps.deleteAllError
    ) {
      this.openSnackBar("success", "Xoá toàn bộ ảnh thành công");
      this.props.onRemoveCache();
    }
    if (
      nextProps &&
      !!!nextProps.isDeleteAll &&
      !!!nextProps.deleteAllSuccess &&
      nextProps.deleteAllError
    ) {
      this.openSnackBar(
        "error",
        `Xoá ảnh thất bại ! ${nextProps.uploadError}`
      );
      this.props.onRemoveCache();
    }
  }

  changeUpdateStatus = () => {
    const { detailHotel } = this.props;
    this.setState({
      isUpdate: true,
      name: detailHotel.name,
      address: detailHotel.address,
      description: detailHotel.description,
      wifi: detailHotel.facilities.wifi,
      laundry: detailHotel.facilities.laundry,
      bar: detailHotel.facilities.bar,
      active: detailHotel.active,
      status: detailHotel.status
    });
  };

  getBody = () => {
    let data = this.props.detailHotel;
    data.name = this.state.name;
    data.address = this.state.address;
    data.facilities.wifi = this.state.wifi;
    data.facilities.laundry = this.state.laundry;
    data.facilities.bar = this.state.bar;
    data.description = this.state.description;

    // delete data.id;
    // delete data.active;
    // delete data.point;
    // delete data.ratings;

    return JSON.stringify(data);
  };

  update = () => {
    const { detailHotel, accessToken } = this.props;
    this.props.onUpdate(detailHotel.id, this.getBody(), accessToken);
  };

  getBodyUpdateStatus = () => {
    const params = {
      message: "string",
      status: !this.state.status
    };
    return JSON.stringify(params);
  };

  changeStatus = () => {
    const { accessToken } = this.props;
    this.props.onChangeStatus(
      this.props.detailHotel.id,
      this.getBodyUpdateStatus(),
      accessToken
    );
  };

  openSnackBar = (type, message) => {
    const obj = {
      variant: type,
      message: message
    };
    this.refs.MySnackBar.handleOption(obj);
    this.refs.MySnackBar.handleOpen();
  };

  cancel = () => {
    this.setState({ isUpdate: false });
  };

  handleChange = name => {
    switch (name) {
      case "wifi":
        this.setState({ wifi: !this.state.wifi });
        break;
      case "laundry":
        this.setState({ laundry: !this.state.laundry });
        break;
      case "bar":
        this.setState({ bar: !this.state.bar });
        break;
      default:
        break;
    }
  };

  showUpdateImage = () => {
    this.setState({ show: !this.state.show });
  };

  handleLogo = event => {
    console.log("logo", event.target.files[0]);
    this.setState({ logo: event.target.files[0] });
  };

  handlePhoto = event => {
    console.log("photo", event.target.files[0]);
    this.setState({ photo: event.target.files[0] });
  };

  requestDelete = () =>{
    this.refs.MyDialog.handleClickOpen("Xoá toàn bộ ảnh", "Bạn có chắc muốn xoá hết ảnh ?", true)
  }

  deleteAllImage = () => {
    const {accessToken,onDeleteAll,detailHotel} = this.props

    var params = {type:'all'}

    onDeleteAll(detailHotel.id, JSON.stringify(params),accessToken)
  }

  updateImage = type => {
    const { accessToken, detailHotel } = this.props;
    const { logo, photo } = this.state;
    // console.log('object')
    // console.log('photo', photo)
    // console.log('logo', logo)
    // var formData = new FormData()
    // formData.append('file',logo);
    // formData.append('type', type)
    // console.log('formData',formData)
    // this.props.onUploadImage(detailHotel.id, formData, accessToken)
    switch (type) {
      case "logo":
        {
          if (type === "logo" && !logo) {
            console.log("1");
            alert("Vui lòng chọn ảnh !");
            return;
          }
          if (logo.size > 3000000) {
            alert("Vui lòng chọn ảnh có dung lượng dưới 1MB");
            return;
          }
          var formData = new FormData();
          formData.append("file", logo);
          formData.append("type", type);
          this.props.onUploadImage(detailHotel.id, formData, accessToken);
        }
        break;

      case "photo":
        {
          if (type === "photo" && !photo) {
            console.log("2");
            alert("Vui lòng chọn ảnh !");
            return;
          }
          if (photo.size > 3000000) {
            alert("Vui lòng chọn ảnh có dung lượng dưới 1MB");
            return;
          }
          var formData = new FormData();
          formData.append("file", photo);
          formData.append("type", type);
          this.props.onUploadImage(detailHotel.id, formData, accessToken);
        }
        break;
      default:
        break;
    }
  };

  render() {
    const {
      classes,
      detailHotel,
      isLoadingHotel,
      isUpdating,
      isChangeStatus,
      onBack,
      isUpload,
      isDeleteAll
    } = this.props;
    const {
      isUpdate,
      name,
      address,
      description,
      wifi,
      laundry,
      bar,
      show
    } = this.state;
    return (
      <Card>
        <CardHeader color="primary">
          <h4 className={classes.cardTitleWhite}>{detailHotel.name}</h4>
          <p className={classes.cardCategoryWhite}>Chi tiết khách sạn </p>
        </CardHeader>
        {isLoadingHotel ? (
          <Loading />
        ) : (
          <CardBody>
            <GridContainer>
              <GridItem xs={12} sm={12} md={5}>
                <TextField
                  disabled={!isUpdate}
                  id="standard-name"
                  label="Tên khách sạn "
                  value={isUpdate ? name : detailHotel.name}
                  onChange={event =>
                    this.setState({ name: event.target.value })
                  }
                  margin="normal"
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={3}>
                <TextField
                  disabled={!isUpdate}
                  id="standard-address"
                  label="Địa chỉ "
                  value={isUpdate ? address : detailHotel.address}
                  onChange={event =>
                    this.setState({ address: event.target.value })
                  }
                  margin="normal"
                />
              </GridItem>
            </GridContainer>
            <GridContainer
              style={{
                alignItems: "center",
                paddingTop: 15,
                paddingBottom: 15
              }}
            >
              <h3
                style={{
                  fontSize: 16,
                  marginLeft: "15px",
                  marginTop: 0,
                  marginBottom: 0,
                  padding: 0
                }}
              >
                Dịch vụ cung cấp
              </h3>
              <GridItem xs={12} sm={12} md={3}>
                <Grid container alignItems="center" direction="row">
                  {isUpdate ? (
                    <Checkbox
                      checked={wifi}
                      onChange={() => this.handleChange("wifi")}
                    />
                  ) : (
                    <CardMedia
                      style={{ width: "20px", height: "20px" }}
                      image={detailHotel.facilities.wifi ? check : unCheck}
                      title="Paella dish"
                    />
                  )}
                  <h3
                    style={{
                      fontSize: 16,
                      padding: 0,
                      marginLeft: "20px",
                      marginBottom: 0,
                      marginTop: 0
                    }}
                  >
                    Wifi
                  </h3>
                </Grid>
              </GridItem>
              <GridItem xs={12} sm={12} md={3}>
                <Grid container alignItems="center" direction="row">
                  {isUpdate ? (
                    <Checkbox
                      checked={laundry}
                      onChange={() => this.handleChange("laundry")}
                    />
                  ) : (
                    <CardMedia
                      style={{ width: "20px", height: "20px" }}
                      image={detailHotel.facilities.laundry ? check : unCheck}
                      title="Paella dish"
                    />
                  )}
                  <h3
                    style={{
                      fontSize: 16,
                      padding: 0,
                      marginLeft: "20px",
                      marginBottom: 0,
                      marginTop: 0
                    }}
                  >
                    Laundry
                  </h3>
                </Grid>
              </GridItem>
              <GridItem xs={12} sm={12} md={3}>
                <Grid container alignItems="center" direction="row">
                  {isUpdate ? (
                    <Checkbox
                      checked={bar}
                      onChange={() => this.handleChange("bar")}
                    />
                  ) : (
                    <CardMedia
                      style={{ width: "20px", height: "20px" }}
                      image={detailHotel.facilities.bar ? check : unCheck}
                      title="Paella dish"
                    />
                  )}
                  <h3
                    style={{
                      fontSize: 16,
                      padding: 0,
                      marginLeft: "20px",
                      marginBottom: 0,
                      marginTop: 0
                    }}
                  >
                    Bar
                  </h3>
                </Grid>
              </GridItem>
            </GridContainer>
            <GridContainer
              style={{
                alignItems: "center",
                paddingTop: 15,
                paddingBottom: 15
              }}
            >
              <h3
                style={{
                  fontSize: 16,
                  marginLeft: "15px",
                  marginTop: 0,
                  marginBottom: 0,
                  padding: 0
                }}
              >
                Trạng thái
              </h3>
              <GridItem xs={12} sm={12} md={4}>
                <Grid container alignItems="center" direction="row">
                  <CardMedia
                    style={{ width: "20px", height: "20px" }}
                    image={detailHotel.active ? check : unCheck}
                    title="Paella dish"
                  />
                  <h3
                    style={{
                      fontSize: 16,
                      padding: 0,
                      marginLeft: "20px",
                      marginBottom: 0,
                      marginTop: 0
                    }}
                  >
                    Đang hoạt động
                  </h3>
                </Grid>
              </GridItem>
              <GridItem xs={12} sm={12} md={4}>
                <Grid container alignItems="center" direction="row">
                  <CardMedia
                    style={{ width: "20px", height: "20px" }}
                    image={detailHotel.status ? check : unCheck}
                    title="Paella dish"
                  />
                  <h3
                    style={{
                      fontSize: 16,
                      padding: 0,
                      marginLeft: "20px",
                      marginBottom: 0,
                      marginTop: 0
                    }}
                  >
                    Còn phòng
                  </h3>
                </Grid>
              </GridItem>
            </GridContainer>
            <GridContainer>
              <GridItem xs={12} sm={12} md={12}>
                <h3
                  style={{
                    fontSize: 16,
                    marginTop: 15,
                    marginBottom: 15,
                    padding: 0
                  }}
                >
                  {`Số lượt đánh giá : ${detailHotel.ratings}`}
                </h3>
                <h3
                  style={{
                    fontSize: 16,
                    marginTop: 15,
                    marginBottom: 15,
                    padding: 0
                  }}
                >
                  {`Điểm  : ${Math.floor(detailHotel.point * 10) / 10} / 5`}
                </h3>
                <h3
                  style={{
                    fontSize: 16,
                    marginTop: 15,
                    marginBottom: 15,
                    padding: 0
                  }}
                >
                  {`Số điện thoại  : ${
                    detailHotel.phone_number
                      ? detailHotel.phone_number
                      : "Chưa có"
                  }`}
                </h3>
              </GridItem>
            </GridContainer>
            <GridContainer>
              {detailHotel.logo && detailHotel.logo !== "" &&
              <GridItem xs={12} sm={12} md={3}>
                <h3
                  style={{
                    fontSize: 16,
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Ảnh logo khách sạn
                </h3>
                <img src={`${ANOTHER_HOST}${detailHotel.logo}`} width={200} alt={'...'} />
              </GridItem>}
             {detailHotel.photos && detailHotel.photos.length !== 0 &&  <GridItem xs={12} sm={12} md={9}>
                <h3
                  style={{
                    fontSize: 16,
                    marginTop: 0,
                    marginBottom: 0,
                    padding: 0
                  }}
                >
                  Danh mục hình ảnh
                </h3>
                <GridList className={classes.gridList} cols={1}>
                  {detailHotel.photos.map(item => (
                    <img src={item} height={100} alt={item} />
                  ))}
                </GridList>
              </GridItem>}
            </GridContainer>
            {show && (
              <GridContainer>
                <GridItem xs={12} sm={12} md={4}>
                  <h3
                    style={{
                      fontSize: 16,
                      marginTop: 15,
                      marginBottom: 15,
                      padding: 0
                    }}
                  >
                    {`Cập nhật logo khách sạn `}
                  </h3>
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <input
                    style={{
                      fontSize: 16,
                      marginTop: 15,
                      marginBottom: 15,
                      padding: 0
                    }}
                    type="file"
                    onChange={event => this.handleLogo(event)}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <Button
                    disabled={
                      isUpdating || isChangeStatus || isLoadingHotel || isUpload || isDeleteAll
                    }
                    color="primary"
                    onClick={() => this.updateImage("logo")}
                  >
                    Đăng ảnh
                  </Button>
                </GridItem>
              </GridContainer>
            )}
            {show && (
              <GridContainer>
                <GridItem xs={12} sm={12} md={4}>
                  <h3
                    style={{
                      fontSize: 16,
                      marginTop: 15,
                      marginBottom: 15,
                      padding: 0
                    }}
                  >
                    {`Cập nhật ảnh cho khách sạn `}
                  </h3>
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <input
                    style={{
                      fontSize: 16,
                      marginTop: 15,
                      marginBottom: 15,
                      padding: 0
                    }}
                    type="file"
                    onChange={event => this.handlePhoto(event)}
                  />
                </GridItem>
                <GridItem xs={12} sm={12} md={4}>
                  <Button
                    disabled={
                      isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                    }
                    color="primary"
                    onClick={() => this.updateImage("photo")}
                  >
                    Đăng ảnh
                  </Button>
                </GridItem>
              </GridContainer>
            )}
            <GridContainer style={{ marginTop: 15, marginBottom: 15 }}>
              <GridItem xs={12} sm={12} md={12}>
                <TextField
                  disabled={!isUpdate}
                  fullWidth={true}
                  id="standard-description"
                  label="Description"
                  value={isUpdate ? description : detailHotel.description}
                  onChange={event =>
                    this.setState({ description: event.target.value })
                  }
                  margin="normal"
                />
              </GridItem>
            </GridContainer>
          </CardBody>
        )}
        <CardFooter>
          {!isUpdate ? (
            <Grid container direction="row">
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                style={{ marginRight: 20 }}
                color="primary"
                onClick={() => this.changeUpdateStatus()}
              >
                Cập nhật
              </Button>
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                color="primary"
                style={{ marginRight: 20 }}
                onClick={this.changeStatus}
              >
                Chuyển trạng thái
              </Button>
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                color="primary"
                style={{ marginRight: 20 }}
                onClick={this.showUpdateImage}
              >
                {!show ? "Cập nhật ảnh" : "Huỷ cập nhật"}
              </Button>
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                color="primary"
                style={{ marginRight: 20 }}
                onClick={this.requestDelete}
              >
                Xoá ảnh
              </Button>
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                color="primary"
                onClick={onBack}
              >
                Quay lại
              </Button>
            </Grid>
          ) : (
            <Grid container direction="row">
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                style={{ marginRight: 20 }}
                color="primary"
                onClick={() => this.update()}
              >
                Xong
              </Button>
              <Button
                disabled={
                  isUpdating || isChangeStatus || isLoadingHotel || isUpload|| isDeleteAll
                }
                color="primary"
                onClick={() => this.cancel()}
              >
                Quay lại
              </Button>
            </Grid>
          )}
        </CardFooter>
        <MySnackBar ref={"MySnackBar"} />
        <MyDialog ref={"MyDialog"} logout={this.deleteAllImage} />
      </Card>
    );
  }
}

const mapStateToProps = state => {
  return {
    accessToken: state.profileReducer.accessToken,

    detailHotel: state.detailHotelReducer.detailHotel,
    isLoadingHotel: state.detailHotelReducer.isLoadingHotel,
    getHotelSuccess: state.detailHotelReducer.getHotelSuccess,
    errorGetHotel: state.detailHotelReducer.errorGetHotel,

    isUpdating: state.detailHotelReducer.isUpdating,
    updateSuccess: state.detailHotelReducer.updateSuccess,
    errorUpdate: state.detailHotelReducer.errorUpdate,

    isChangeStatus: state.detailHotelReducer.isChangeStatus,
    errorChangeStatus: state.detailHotelReducer.errorChangeStatus,
    changeStatusSuccess: state.detailHotelReducer.changeStatusSuccess,

    isUpload: state.detailHotelReducer.isUpload,
    uploadSuccess: state.detailHotelReducer.uploadSuccess,
    uploadError: state.detailHotelReducer.uploadError,

    isDeleteAll: state.detailHotelReducer.isDeleteAll,
    deleteAllSuccess: state.detailHotelReducer.deleteAllSuccess,
    deleteAllError: state.detailHotelReducer.deleteAllError,
  };
};

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  },
  gridList: {
    flexWrap: "nowrap",
    transform: "translateZ(0)"
  }
};

const mapDispatchToProps = dispatch => {
  return {
    onGetDetail: id => {
      dispatch(getDetailHotel(id));
    },
    onUpdate: (id, body, token) => {
      dispatch(updateHotel(id, body, token));
    },
    onRemove: () => {
      dispatch(removeData());
    },
    onChangeStatus: (id, body, token) => {
      dispatch(changeStatus(id, body, token));
    },
    onUploadImage: (id, body, token) => {
      dispatch(uploadImage(id, body, token));
    },
    onRemoveCache: () => {
      dispatch(removeCache());
    },
    onDeleteAll : (id,body,token) =>{
      dispatch(deleteAllImage(id,body,token))
    }
  };
};

const redux = connect(
  mapStateToProps,
  mapDispatchToProps
)(InfoDetail);

export default withStyles(styles)(redux);
